@charset "utf-8";

@mixin iconFilter {
  filter: invert(98%) sepia(10%) saturate(48%) hue-rotate(76deg) brightness(119%) contrast(100%);
}

@mixin boxRec ($bg: $white) {
  border-radius: 1rem;
  background: rgba($bg, 0.12);
}

.main-home,
.main-health,
.main-reward {
  background: 50% 0 no-repeat;
  background-size: cover;
  padding: 0;

  header {
    transition: all 0.3s;
    background: transparent;

    .right-icons {
      .icon-camera_02, .icon-alarm::before, .icon-menu {
        @include iconFilter;
      }
    }

    &.vh-scroll {
      background: $white;

      .right-icons {
        .icon-camera_02, .icon-alarm::before, .icon-menu {
          filter: none;
        }
      }
    }
  }
  .content-wrap {
    padding: {
      top: calc(constant(safe-area-inset-top) + 5.6rem);
      top: calc(env(safe-area-inset-top) + 5.6rem);
    }
  }

  .scrollBox {
    position: relative !important;
    height: inherit !important;
    padding: 1.3rem 1.6rem 0;
    padding: {
      bottom: calc(constant(safe-area-inset-bottom));
      bottom: calc(env(safe-area-inset-bottom));
    }
  }
}

/* home -  */
.main-home {
  background-image: url('../img/main/home.png');
  transition: background $base-duration;

  &.active-sleep {
    background-image: url('../img/main/home_sleep.png');
  }
}

.home-top-buttons {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;

  button {
    color: $white;
    display: flex;
    align-items: center;

    span {
      @extend .kb-font;
      @extend .font-size-16;
      font-weight: bold;
      line-height: 1.25;
      margin: 0.6rem;
    }
  }
}

.home-info {
  position: absolute;
  width: 81.6%;
  right: 1.6rem;
  bottom: 8.4rem;
  color: $white;

  &__inner {
    position: relative;
  }

  &__top-buttons {
    text-align: right;

    button {
      color: $white;
      margin-left: 2rem;
    }
  }

  &__top {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 4rem;

    &-name {
      @extend .kb-font;
      @extend .font-size-24;
      font-weight: bold;
      line-height: 1.24;
    }

    &-weather {
      @extend .kb-font;
      @extend .font-size-16;
      line-height: 1.25;
      font-weight: 500;
      display: flex;
      align-items: center;

      i {
        margin-left: 0.6rem;
      }
    }
  }

  &__weather-text {
    display: flex;
    align-items: center;
    width: 100%;
    margin-top: 0.6rem;

    p {
      font-size: 1.4rem;
      line-height: 1.48;
      margin: 0 0.4rem 0 0;
    }

    button  {
      i {
        @include iconFilter;
      }
    }
  }

  &__weather-etc {
    margin-top: 2rem;

    ul {
      display: flex;
    }

    li {
      flex: 1 0 0;
      text-align: center;
      height: 6.4rem;
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
      @include boxRec;

      & + li {
        margin-left: 1.2rem;
      }
    }
    span {
      @extend .font-size-12;
      font-weight: 500;
      line-height: 1.48;
      display: block;
    }
    em {
      @extend .kb-font;
      @extend .font-size-16;
      line-height: 1.25;
      display: block;
      margin-top: 0.5rem;
    }
  }

  &__active-progress {
    @include boxRec;
    margin-top: 3rem;
    padding: 1.2rem 1.5rem 1.225rem;

    &-title {
      position: relative;
      margin-bottom: 1rem;

      em {
        @extend .kb-font;
        @extend .font-size-16;

        display: block;
        line-height: 1.25;
      }

      button {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        color: $white;

        &.only-icon i { @include iconFilter; }
      }
    }

    &-data {
      @extend .font-size-14;
      line-height: 1.48;
      display: flex;
      align-items: center;

      span {
        display: flex;
        align-items: center;

        & + span {
          &::before {
            content: '';
            position: relative;
            top: 0.1rem;
            display: inline-block;
            width: 1px;
            height: 11px;
            background: $white;
            line-height: 1;
            margin: 0 1.1rem;
          }
        }
      }
    }
  }
}

.home-health-compare {
  position: absolute;
  right: 0;
  bottom: 0;
  width: calc(81.6% + 1.6rem);
  background: $black;
  background: rgba($brand-g, 0.95);
  z-index: 2;

  &__inner {
    position: relative;
  }

  &__title {
    button {
      @extend .kb-font;
      @extend .font-size-16;

      position: relative;
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      height: 5.4rem;
      padding: 0 1.6rem 0 2.5rem;
      line-height: 1.25;
      color: $white;

      &::after {
        @extend .icon-select;
        @extend .icon-18;
        @include iconFilter;

        content: '';
        transform: rotate(180deg);
        transition: all $base-duration;

        .active & {
          transform: rotate(0);
        }
      }
    }
  }

  &__content {
    display: none;
    padding: 2.8rem 2rem 1.45rem 2.5rem;
    height: 46.35rem;
  }

  .tab {
    text-align: left;
    border-bottom: none;

    &::before {
      display: none;
    }

    &__item {
      margin: 0 2.55rem 0 0;

      &::before {
        display: none;
      }

      span {
        @extend .noto-font;
        @extend .font-size-14;
        line-height: 1.48;
        color: $white;
        padding: 0 0 1.3rem;

        &::after {
          content: '';
          position: absolute;
          bottom: 0;
          left: 50%;
          right: 50%;
          width: inherit;
          height: 0.2rem;
          opacity: 0;
          background: $white;
          transition: all 0.2s;
        }
      }

      &--on {
        span {
          &::after {
            left: 0;
            right: 0;
            opacity: 1;
          }
        }
      }
    }

    &__swiper {
      height: calc(46.35rem - 7.35rem);
    }
  }

  .message-box {
    background: transparent;
    padding-bottom: 3rem;

    &__icon {
    }

    &__sub-title {
      font-size: 1.4rem;
      color: $white;
    }

    &__btn-wrap {
      margin-top: 4rem;
    }
  }
}

.home-health-info {
  $root: &;

  color: $white;

  > * {
    padding-right: 0.5rem;
  }

  &__text1 {
    margin-top: 3rem;
    font-size: 1.2rem;
    line-height: 1.48;

    em {
      font-weight: bold;
    }
  }

  &__text2 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 2.5rem;
    font-size: 2rem;
    line-height: 1.24;

    span {
      @extend .kb-font;
    }

    em {
      @extend .kb-font;
      display: flex;
      align-items: center;
      position: relative;
      top: 0.2rem;
    }
  }

  &__text3 {
    margin-top: 1rem;
    font-size: 1.4rem;
    line-height: 1.48;

    em {
      font-weight: bold;
      margin-left: 0.5rem;
    }
  }

  &__time {
    margin-top: 1.5rem;
    display: flex;

    li {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 0.8rem;
      border-radius: 0.5rem;
      background: rgba($white, 0.12);
      height: 2.8rem;
      padding: 0 1rem;
      font-size: 1.2rem;
      white-space: nowrap;
    }
  }

  &__star {
    display: flex;
    align-items: center;
    margin-top: 1.5rem;

    span {
      font-size: 1.4rem;
      line-height: 1.48;
      margin-right: 1.5rem;
    }

    i {
      margin-right: 0.5rem;
    }
  }

  &__graph {
    margin-top: 2.5rem;

    &-box {
      @extend .font-size-12;
      position: relative;
      color: $white;
    }

    &-label {
      text-align: right;
      color: $white;
      line-height: 1.48;

      span {
        margin-right: 1.5rem;
      }

      label {
        & + label {
          margin-left: 1rem;
        }

        &::before {
          content: '';
          width: 0.75rem;
          height: 0.75rem;
          display: inline-block;
          border-radius: 100%;
          margin-right: 0.3rem;
        }

        &:nth-of-type(1)::before {
          background: $secondary-green;
        }

        &:nth-of-type(2)::before {
          background: $brand-y;
        }
      }
    }

    &-grid {
      margin-top: 3.15rem;

      li {
        position: relative;
        line-height: 0;
        font-weight: 200;
        font-size: 0;
        border-bottom: 1px dotted  #8b847e;
        &:last-child {
          border-bottom-style: solid;
        }
        & + li {
          margin-top: 2.5rem;
        }
      }
    }

    &-table {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 100%;

      ul {
        position: relative;
        display: flex;
        height: 100%;
        justify-content: space-around;
      }

      li {
        flex: 0 0 1;
        text-align: center;
        position: relative;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;

        span {
          position: absolute;
          left: -30px;
          right: -30px;
          bottom: -2rem;
        }

        div {
          position: relative;
          height: 100%;
          display: flex;
          justify-content: space-between;
          align-items: flex-end;
          width: 4rem;
        }

        i {
          display: block;
          width: 0.8rem;
          background: $secondary-green;
          margin: 0;
          position: relative;

          &:nth-of-type(2) {
            background: $brand-y;
          }
        }

        strong {
          position: absolute;
          left: 50%;
          bottom: 100%;
          white-space: nowrap;
          transform: translateX(-50%);
          font-style: normal;
          margin-bottom: 0.5rem;
          font-weight: 500;
        }
      }
    }
  }

  &__progress {
    margin-top: 6.6rem;
    height: 7.3rem;

    #{$root}__star + & {
      margin-top: 3rem;
    }

    ul {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 100%;
      height: 100%;
      position: relative;
      &::before {
        content: '';
        position: absolute;
        left: 2.725rem;
        top: 0;
        bottom: 0;
        width: 1px;
        background: #978f87;
      }
    }

    li {
      display: flex;
      width: 100%;
      justify-content: flex-start;
      align-items: center;
    }

    label {
      font-size: 1.2rem;
      line-height: 1.48;
      width: 3.7rem;
      position: relative;
      top: -0.1rem;
    }

    &-bar {
      @include progress(rgba($gray-e, 0.15));

      height: 0.8rem;
      overflow: inherit;

      span {
        position: relative;
      }

      strong {
        margin-top: 0.5rem;
        position: absolute;
        top: 100%;
        right: 0;
        font-size: 1.2rem;
        font-weight: bold;
        line-height: 1.48;
        white-space: nowrap;
      }

      &.today {
        span {
          background-color: $secondary-green;
        }
      }
    }
  }
}


/* home -  */
.main-health {
  background-image: url('../img/main/home_health.png');

  .scrollBox {
    padding-bottom: 2.875rem;
  }

  &__score, &__boxes {
    padding-left: 2.2rem;
  }

  &__score {
    color: $white;
    margin-top: 4rem;

    &-button {
      display: flex;
      justify-content: flex-start;
      align-items: center;

      span {
        @extend .kb-font;
        font-size: 2.5rem;
        line-height: 1.24;
        color: $white;
        text-align: left;
        font-weight: 500;
        margin-right: 0.8rem;
      }

      i {
        @include iconFilter;
      }
    }

    &-text1 {
      margin: 0.6rem 0 2rem;
      font-size: 1.4rem;
      line-height: 1.48;
    }

    &-text2 {
      margin-bottom: 1.5rem;
      font-size: 1.6rem;
      line-height: 1.47;
      margin-right: 5rem;
    }
  }

  &__boxes {
    color: $white;
    text-align: center;

    > * {
      margin-top: 2.4rem;
    }
  }

  &__box {
    &-col1 {
      @include boxRec;
      height: 12.1rem;
      padding: 2rem;

      &-title {
        display: flex;
        width: 100%;
        justify-content: space-between;
        align-items: center;

        em {
          @extend .kb-font;
          font-size: 1.6rem;
          font-weight: 500;
          line-height: 1.25;
        }
        span {
          font-size: 1.4rem;
          line-height: 1.23;
        }
      }

      &-buttons {
        text-align: right;

        button {
          color: $white;
          margin-left: 1.5rem;
        }
      }
    }

    &-col3 {
      ul {
        display: flex;
        flex-wrap: wrap;
        margin: -0.9rem 0 0 -0.9rem;
      }
      li {
        @include boxRec;
        margin: 0.9rem 0 0 0.9rem;
        flex: 0 1 calc(33.3% - 0.9rem);
        height: 9.2rem;
        align-items: center;
      }
    }
  }

  &__box-list-type1 {
    button {
      height: 100%;
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    span {
      display: block;
      color: $white;
      font-size: 1.4rem;
      line-height: 1.48;
      font-weight: 500;
      margin-top: 1.1rem;
    }
  }

  &__box-list-type2 {
    li {
      display: flex;
      flex-direction: column;
      padding-top: 1.5rem;
    }
    label {
      font-size: 1.4rem;
      font-weight: 500;
      line-height: 1.48;
      margin-bottom: 1.05rem;
    }
    span {
      @extend .kb-font;
      display: block;
      font-size: 1.6rem;
      line-height: 0.94;
      margin-bottom: 0.5rem;
    }
    i {
      @extend .kb-font;
      display: inline-block;
      font-size: 1.2rem;
      line-height: 0.94;
      font-style: normal;
    }
    .icon-circle_check {
      @include iconFilter;

      &.off {
        opacity: 0.3;
      }
    }
  }

  &__box-list-type3 {
    margin-top: 1.75rem;
    display: flex;

    li {
      flex: 1 0 0;

      & + li {
        border-left: 1px solid rgba($white, 0.3);
      }

      label {
        display: block;
        font-size: 1.2rem;
        font-weight: 500;
        line-height: 1.48;
      }
      span {
        display: block;
        margin-top: 0.8rem;
        font-size: 1.6rem;
        line-height: 1.25;
        font-weight: 500;
      }
      em {
        display: block;
        margin-top: 0.7rem;
        font-size: 1.8rem;
        line-height: 1.24;
        font-weight: 500;
      }
    }
  }

  &__box-list-type4 {
    p {
      margin-top: 1.7rem;
      font-size: 1.4rem;
      font-weight: 500;
      line-height: 1.48;
    }

    ul {
      margin-top: 1.75rem;
      display: flex;
      text-align: left;
    }

    li {
      & + li {
        border-left: 1px solid rgba($white, 0.3);
        margin-left: 2.9rem;
        padding-left: 2.9rem;
      }

      span {
        display: block;
        margin-top: 0.8rem;
        font-size: 1.6rem;
        line-height: 1.25;
        font-weight: 500;
      }
    }
  }

  &__box-slide {
    .swiper {
      &-container {
        position: relative;
        margin-right: -1.6rem;
        padding-right: 1.6rem;
      }

      &-slide {
        padding-right: 1rem;

        div {
          @include boxRec;
          height: 10.2rem;
          padding: 1.5rem 2rem;
          text-align: left;
          color: $white;
        }

        em {
          display: block;
          font-size: 1.6rem;
          font-weight: 500;
          line-height: 1.5;
          margin-bottom: 0.8rem;
          position: relative;
          margin-top: -0.2rem;
        }

        p {
          font-size: 1.4rem;
          line-height: 1.48;
          font-weight: 300;
          word-break: keep-all;
        }

        strong {
          vertical-align: top;
          font-weight: 500;
        }
      }

      &-slide-active {
        font-size: 2rem;
        color: $black;
      }
    }
  }
}

.main-top-image {
  .col-type1 {
    background: #776c61;
    padding: 0;
    overflow: hidden;

    .main-health__box-col1-buttons {
      background: rgba($black, 0.13);
      padding: 1rem 1.5rem;
      border-radius: 1rem;
    }

    .main-health__box-list-type4 {
      padding: 0 2rem;
    }
  }

  .col-type2 {
    background: #f6f7fa;

    .main-health__box-col1-title {
      em {
        display: inline-flex;
        align-items: center;
        color: $black;

        i {
          color: #76b424;
          font-style: normal;
          margin-left: 0.5rem;
        }
      }
    }
  }

  .main-health__box-list-type1 {
    li:first-child {
      flex-basis: calc(33.3% * 2 - 0.9rem);
      background: $black;
      white-space: nowrap;
      display: flex;
      justify-content: space-evenly;

      button {
        width: inherit;

        &:nth-of-type(1) {
          order: 1;
        }

        &:nth-of-type(2) {
          order: 3;
        }
      }

      &::before {
        content: '';
        width: 1px;
        height: 4.8rem;
        background: rgba(#efefef, 0.3);
        order: 2;
      }
    }

    li:last-child {
      background: $secondary-green;

      span {
        font-size: 1.8rem;
        line-height: 1.24;
        margin-top: 0;
      }
    }
  }

  .main-health__box-list-type2 {
    li {
      background: #999088;
      align-items: flex-start;
      justify-content: flex-end;
      padding: 0 1.3rem;
      position: relative;
      overflow: hidden;

      &::before {
        position: absolute;
        right: 34%;
        top: 0;
        width: 0px;
        height: 0px;
        content: '';
        border-bottom: 1.3rem solid transparent;
        border-right: 1.3rem solid $secondary-gold2;
      }

      &::after {
        background: $secondary-gold2;
        content: '';
        position: absolute;
        right: 0;
        top: 0;
        height: 1.3rem;
        width: 34%;
      }
    }

    span {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      height: 3.5rem;
      margin-bottom: 0.8rem;
      width: 100%;
      white-space: nowrap;
      line-height: 1.5;

      &.only-icon {
        align-items: center;
        padding-bottom: 1.5rem;
      }
    }
  }

  .main-health__box-list-type3 {
    margin-top: 1.75rem;
    display: flex;

    li {
      flex: 1 0 0;

      & + li {
        border-color: #d3d4d6;
      }

      label {
        color: $gray-9;

        &.bold {
          color: $gray-6;
        }
      }

      span {
        color: $black;
      }

      em {
        color: $black;

        &.point {
          color: $secondary-green;
        }
      }
    }
  }
}


/* reward -  */
.main-reward {
  $root: &;

  background-image: url('../img/main/home_reward.png');

  .scrollBox {
    padding-bottom: 4rem;
  }

  &__top, &__boxes {
    padding-left: 2.2rem;
  }

  &__top {
    color: $white;
    margin-top: 4rem;

    &-title {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 5.775rem;

      span {
        @extend .kb-font;
        font-size: 2.5rem;
        line-height: 1.24;
        color: $white;
        text-align: left;
        font-weight: 500;
      }

      p {
        font-size: 1.4rem;
        line-height: 1.48;
      }
    }
  }

  &__top-list {
    display: flex;
    width: 100%;
    color: $white;
    margin-top: 2rem;

    i {
      @include iconFilter;
    }

    li + li {
      //padding-left: 2rem;
      //margin-left: 2rem;
      padding-left: 1.5rem;
      margin-left: 1.4rem;
      position: relative;

      &::before {
        content: '';
        display: block;
        width: 0.1rem;
        height: 5.5rem;
        background: rgba($white, 0.3);
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }

    &-title {
      display: flex;
      align-items: center;
      span {
        font-size: 1.4rem;
        line-height: 1.48;
        font-weight: 500;
      }
    }

    &-button {
      display: flex;
      align-items: center;
      color: $white;

      i {
        margin-left: 0.8rem;
      }
      strong {
        @extend .kb-font;
        //font-size: 3.5rem;
        font-size: 2.4rem;
        line-height: 1.24;
        vertical-align: top;
        white-space: nowrap;
      }
      span {
        @extend .kb-font;
        font-size: 2.25rem;
        line-height: 1.23;
        position: relative;
      }
    }
  }

  &__box-title {
    @extend .kb-font;

    font-size: 1.8rem;
    line-height: 1.67;
    text-align: left;
