/*   */
.main-event-popup {
  background: $white;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: $popup-z-index;
  padding: 0;
  display: block;

  &__outer {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 0 0 4.5rem;
  }

  &__inner {
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    height: 100%;
    height: calc(100% - constant(safe-area-inset-bottom));
    height: calc(100% - env(safe-area-inset-bottom));
  }

  &__images {
    img {
      max-width: 100%;
    }

    .swiper {
      &-pagination {
        position: relative;
        text-align: center;
        margin-top: 1.5rem;

        &-bullet {
          width: 1rem;
          height: 1rem;
          margin: 0 0.5rem;

          &-active {
            background: $brand-y;
          }
        }
      }
    }
  }

  &__detail {
    display: none;
    &.on {
      display: block;
    }
  }

  &__content {
    padding: 2.5rem 1.6rem 3rem;

    .text1 {
      font-size: 1.8rem;
      line-height: 1.67;
      color: $black;
    }

    .text2 {
      margin-top: 1rem;
      font-size: 1.4rem;
      line-height: 1.61;
      color: $gray-9;
    }
    .text3 {
      margin-top: 2.5rem;
      position: relative;
      font-size: 1.6rem;
      line-height: 1.66;
      color: $gray-6;
      padding-left: 0.9rem;

      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 1.2rem;
        display: block;
        width: 0.4rem;
        height: 0.4rem;
        border-radius: 100%;
        background: $gray-6;
      }
    }
  }

  &__content-buttons {
    text-align: center;
    margin-top: 2.1rem;
  }

  &__buttons {
    position: absolute;
    left: 0;
    width: 100%;
    bottom: 0;
    display: flex;

    button {
      @extend .font-size-16;

      color: $black;
      border-top: 1px solid $border-color-d;
      width: 50%;
      height: 4.5rem;
      height: calc(4.5rem + constant(safe-area-inset-bottom)) !important;
      height: calc(4.5rem + env(safe-area-inset-bottom)) !important;
      padding-bottom: calc(constant(safe-area-inset-bottom) - 0.8rem);
      padding-bottom: calc(env(safe-area-inset-bottom) - 0.8rem);

      & + button {
        border-left: 1px solid $border-color-d;
      }
    }
  }
}

