.survary-guide {
  text-align: center;
  padding-top: 4rem;

  &__title {
    @extend .kb-font;
    font-size: 2rem;
    font-weight: bold;
    line-height: 1.75;
    text-align: center;
    color: $black;
    margin-top: 4rem;
    width: 100%;
    word-break: keep-all;
  }

  &__text {
    font-size: 1.6rem;
    line-height: 1.66;
    text-align: left;
    color: $black;
    margin-top: 5rem;
    width: 100%;
    word-break: keep-all;
  }

  &__box {
    margin-top: 1.2rem;
    padding: 1.5rem;
    border-radius: 1rem;
    background: $bg-color-f;
    font-size: 1.6rem;
    line-height: 1.66;
    text-align: left;
    color: $gray-6;
    width: 100%;
    word-break: keep-all;
  }

  strong {
    color: $black;
    vertical-align: top;
    font-weight: bold;
  }
}

.consult-time-selector {
  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: -1rem -0.5rem 0;
  }

  &__item {
    height: 5.6rem;
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex: 0 0 calc(20% - 1rem);
    width: calc(20% - 1rem);
    margin: 1rem 0.5rem 0;
    border: 1px solid $border-color-a;
    border-radius: 1rem;
    line-height: 1.45;
    color: $gray-b;

    &:disabled {
      border-style: dashed;
      border-color: $border-color-d;
    }

    &.active {
      border-color: $border-color-7;
      background: $secondary-green;
      color: $white;
    }

    em {
      font-size: 1.6rem;
    }

    span {
      display: block;
      font-size: 1.4rem;
    }
  }

  &__label {
    font-size: 1.2rem;
    color: $black;
    text-align: right;
    margin: 1.5rem 0 1.3rem;

    > span {
      display: inline-flex;
      align-items: center;
      line-height: 1;

      &::before {
        content: '';
        display: inline-block;
        width: 1.6rem;
        height: 1.6rem;
        border-radius: 0.5rem;
        margin-right: 0.3rem;
      }

      &.active::before {
        border: 1px solid $border-color-a;
      }

      &.disabled::before {
        border: 1px dashed $border-color-d;
      }

      & + span {
        margin-left: 1rem;
      }
    }
  }
}

.consult-reception {
  .table-form__item {
    & + .table-form__item {
      padding-top: 3.5rem;
    }

    &-label {
      display: block;

      label {
        font-size: 1.8rem;
        line-height: 1.67;
      }

      p {
        color: $gray-9;
        font-size: 1.4rem;
        line-height: 1.61;
        margin-top: 0.5rem;
      }
    }
  }

  &__tag {
    li {
    }
  }

  .sound-gym__keyword {
    margin-top: 0;

    ul {
      flex-wrap: wrap;
      overflow-y: inherit;
    }

    li {
      margin-top: 1.2rem;
    }
  }
}

.tags-selector {
