@charset "utf-8";

.list__container {
  padding: 1.6rem;
}
.card-item {
  $item: &;
  > button {
    display: block;
    width: 100%;
    padding: 1.5rem;
    border: 1px solid $border-color-d;
    border-radius: 1rem;
    text-align: left;
    span {
      display: block;
    }
  }
  &__title {
    display: block;
    color: $black;
    @extend .font-size-16;
    line-height: 1.66;
    letter-spacing: -0.03em;

    .arrow & {
      white-space: nowrap;
      width: 100%;
      text-overflow: ellipsis;
      overflow: hidden;
    }
  }
  &__tag {
    display: flex !important;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 1.5rem;
    font-size: 0;
    .tag {
      margin-right: 1rem;
      white-space: nowrap;
    }
    &-info {
      $info: &;
      display: inline-block;
      color: $gray-6;
      word-break: keep-all;
      @extend .font-size-14;
    }
  }
  &__date {
    margin-top: 1rem;
    color: $gray-9;
    @extend .font-size-14;
  }
  &__point {
    margin-top: 1rem;
    color: $black;
    @extend .font-size-16;
    line-height: 2.65rem;
    font-weight: 700;
  }
  &.arrow {
    position: relative;
    > button {
      padding: 1.5rem 4.6rem 1rem 1.5rem;
    }
    &::after {
      position: absolute;
      right: 1rem;
      top: 1.5rem;
      @extend .icon-arrow_right;
      @extend .icon-20;
      content: '';
    }
  }
  &__stamp {
    display: none;
    position: absolute;
    width: 6rem;
    height: 6rem;
    top: calc(50% - 3rem);
    right: 1.5rem;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &.disabled {
    position: relative;
    #{$item}__title {
      color: $gray-b;
    }
    #{$item}__tag {
      .tag {
        background-color: $gray-b;
      }
      &-info {
        color: $gray-b;
      }
    }
    #{$item}__date {
      color: $gray-b;
    }
    #{$item}__stamp {
      display: block;
    }
  }
  &#{$item}-img {
    > button {
      padding:0;
      overflow: hidden;
    }
    #{$item}__img {
      img {
        width: 100%;
      }
    }
    #{$item}__text {
      padding: 1rem 1.5rem 1.5rem;
    }
  }
}
.list {
  &-border {
    border-top: 1px solid $border-color-d;
  }
  &-item {
    > button {
      position: relative;
      display: block;
      width: 100%;
      padding: 1.5rem 3.9rem 1.5rem 1.5rem;
      cursor: pointer;
      border-bottom: 1px solid $border-color-d;
      text-align: left;
      &::after {
        position: absolute;
        right: 1rem;
        top: calc(50% - 1rem);
        @extend .icon-arrow_right;
        @extend .icon-20;
        content: '';
      }
      span {
        display: block;
      }
      .increase {
        color: $red;
      }
      .decrease {
        color: $blue;
      }
    }
    &__title {
      display: flex;
      align-items: center;
      color: $black;
      @extend .font-size-16;
      line-height: 1.66;

      strong {
        font-weight: 700;
        vertical-align: top;
      }

      i {
        position: relative;
        top: -0.2rem;
        margin-right: 0.5rem;
      }
    }
    &__date {
      margin-top: 1rem;
      color: $gray-9;
      line-height: 1.43;
      @extend .font-size-14;
    }
    &__both {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      li {
        &.fixed {
          text-align: right;
          margin-left: 1.5rem;
          flex-shrink: 0;
          white-space: nowrap;
        }
      }
    }
    .with-q-icon & {
      > button {
        padding: 1.5rem 4.6rem 1.5rem 4.1rem;
        &::before {
          position: absolute;
          top: 2.1rem;
          left: 1.5rem;
          @extend .icon-qna_q;
          @extend .icon-16;
          content: '';
        }
      }
    }
    .no-icon & {
      > button {
        padding: 1.5rem;
        &::before,
        &::after {
          display: none;
        }
      }
    }
  }
}

.message-box {
  display: table;
  width: 100%;
  height: 100%;
  background-color: $white;
  &__wrap {
    display: table-cell;
    width: 100%;
    height: 100%;
    min-height: 20rem;
    padding: 6rem 0;
    vertical-align: middle;
    text-align: center;
  }
  &__icon {
    margin-bottom: 2rem;
    &.error {
      margin-bottom: 4rem;
      img {
        width: 8rem;
      }
    }
  }
  &__title {
    margin-bottom: 1rem;
    color: $black;
    @extend .font-size-20;
  }

  &__sub-title {
    color: $gray-9;
    line-height: 1.66;
    @extend .font-size-16;

    &.bold {
      color: $gray-6;
      font-weight: bold;
    }
  }

  &__text {
    margin-top: 1rem;
    color: $gray-9;
    line-height: 1.61;
    @extend .font-size-14;
  }

  &__btn-wrap {
    margin-top: 6rem;
    font-size: 0;

    button + button {
      margin-left: 1.2rem;
    }

    &.mt30 {
      margin-top: 3rem;
    }
  }
}

.list-type-circle {
  li, dd {
    position: relative;
    font-size: 1.6rem;
    line-height: 1.66;
    color: $gray-6;
    padding-left: 0.9rem;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 1.2rem;
      display: block;
      width: 0.4rem;
      height: 0.4rem;
      border-radius: 100%;
      background: $gray-6;
    }

    & + li,
    & + dd {
      margin-top: 1.2rem;
    }
  }
}
