@charset "utf-8";

.page-diet {
  display: flex;
  flex-direction: column;
  width: 100%;

  &__top-date {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 6.23rem;
    background: $bg-color-f;
    box-shadow: 0 0 0.2rem rgba(0, 0, 0, 0.8);
    position: relative;
    margin: 0 -1.6rem;
    flex: 0 0 auto;

    strong {
      @extend .kb-font;
      @extend .font-size-18;

      font-weight: bold;
    }

    button {
      position: absolute;
      width: 3rem;
      height: 3rem;
      right: 1.6rem;
      top: 50%;
      margin-top: -1.5rem;
    }
  }

  &__title-h3 {
    @extend .kb-font;
    @extend .font-size-18;

    font-weight: bold;
    line-height: 1.23;

    span {
      @extend .kb-font;
      color: $gray-6;
    }
  }

  &__title-h4 {
    @extend .kb-font;
    @extend .font-size-16;

    font-weight: bold;
    color: $black;
    line-height: 1.375;
  }
}

.diet-main-title {
  padding-left: 5rem;
  position: relative;
  min-height: 4rem;
  margin-top: 4rem;
  flex: 0 0 auto;

  &__icon {
    position: absolute;
    left: 0;
    top: 0;
    border:1px solid #d3d4d6;
    border-radius: 1rem;
    width: 4rem;
    height: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;

    &::after {
      content: '';
      @extend .icon-explain;
      @extend .icon-20;
    }
  }

  &__text {
    @extend .font-size-16;
    line-height: 1.65625;
    color: $gray-6;
    word-break: keep-all;

    strong {
      font-weight: 600;
      color: $black;
      vertical-align: top;
    }
  }
}

/*  */
.nutrition-info {
  background: $bg-color-77;
  border-radius: 1rem;
  color: $white;
  padding: 3rem 2.4rem 4.5rem;
  margin-top: 1.5rem;

  &__title {
    strong {
      @extend .kb-font;
      @extend .font-size-20;
      display: block;
      line-height: 1.24;
    }
    span {
      @extend .font-size-16;
      display: block;
      line-height: 1.40625;
      margin-top: 0.5rem;
      font-weight: 200;
    }
  }

  &__text {
    @extend .font-size-12;
    line-height: 2.08;
    vertical-align: top;
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;

    * {
      vertical-align: top;
    }

    strong {
      font-weight: 600;
    }
  }

  &__kcal {
    display: flex;
    margin-top: 1.5rem;

    span {
      @extend .font-size-14;
      line-height: 1.48;
      display: block;
      font-weight: 200;
    }

    em {
      @extend .kb-font;
      display: block;
      font-size: 3.5rem;
      line-height: 1.24;
      margin-top: 0.4rem;
    }

    li {
      position: relative;
      white-space: nowrap;

      & + li {
        padding-left: 2.3rem;
        margin-left: 2.3rem;

        &::before {
          content: '';
          background: $white;
          opacity: 0.5;
          width: 1px;
          height: 4.5rem;
          display: inline-block;
          position: absolute;
          left: 0;
          top: 50%;
          margin-top: -2.25rem;
        }
      }
    }
  }

  &__progress {
    margin-top: 2rem;

    * {
      white-space: nowrap;
    }

    &-bar {
      height: 0.8rem;
      background: $bg-color-ea;
      border-radius: 0.4rem;

      span {
        display: block;
        height: 0.8rem;
        background: $brand-y;
        line-height: 0;
        border-radius: 0.4rem;
        position: relative;
      }

      em {
        @extend .font-size-12;
        position: absolute;
        right: 0;
        top: 100%;
        margin-top: 0.6rem;
        transform: translateX(50%);
        line-height: 2.08;
        white-space: nowrap;
      }
    }
  }

  &__graph {
    margin-top: 2.78rem;

    &-box {
      @extend .font-size-12;

      position: relative;
    }

    &-grid {
      margin-top: 1.95rem;

      li {
        position: relative;
        line-height: 0;
        font-weight: 200;
        &::before {
          content: '';
          position: absolute;
          left: 4.2rem;
          right: 0;
          height: 1px;
          background: rgba($white, 0.24);
        }
        & + li {
          margin-top: 3.45rem;
        }
      }
    }

    &-table {
      position: absolute;
      left: 4.2rem;
      right: 0;
      bottom: 0;
      height: 100%;

      ul {
        position: relative;
        display: flex;
        height: 100%;
      }

      li {
        flex: 1 0 0;
        text-align: center;
        position: relative;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;

        span {
          position: absolute;
          left: 0;
          bottom: -2rem;
          width: 100%;
          font-weight: 200;
        }

        i {
          display: block;
          width: 0.75rem;
          background: $secondary-green;
          margin: 0 auto;
          position: relative;

        }
        strong {
          position: absolute;
          left: 50%;
          bottom: 100%;
          white-space: nowrap;
          transform: translateX(-50%);
          font-style: normal;
          margin-bottom: 0.5rem;
          font-weight: 500;
        }
      }
    }
  }
}

/*  */
.diet-day-nutrition {
  margin-top: 2.5rem;

  &__info {
    border: solid $border-color-d;
    border-width: 1px 0;
    padding: 1.5rem 0 1.6rem;
    margin-top: 1.2rem;

    * {
      white-space: nowrap;
    }

    li {
      display: flex;
      justify-content: space-between;
      align-items: center;
      text-align: left;

      & + li {
        margin-top: 2rem;
      }

      span {
        @extend .font-size-14;
        line-height: 1.42;
        color: $gray-6;
      }

      em {
        @extend .font-size-14;
        line-height: 1.375;
      }
    }
  }

  &__buttons {
    margin-top: 2rem;
    text-align: center;

    button.text-icon {
      .icon-button {
        @extend .icon-document;
        position: relative;
        top: 0.1rem;
        background-size: auto 100%;
      }
    }
  }

  .diet-detail & {
    border-top: 1px solid #ddd;
    padding-top: 2.5rem;
    margin-top: 1.5rem;
  }

  .diet-detail &__info {
    border: none;
    padding: 0;
    margin-top: 2rem;
  }
}

/*   */
.diet-detail {
  padding-bottom: 4.5rem;

  &.is-only {
    padding-bottom: 1.4rem;
  }

  &__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 4rem;

    .button__flex-wrap {
      width: 12rem;
    }
  }
}

/*   -  */
.diet-photo {
  margin-top: 1.5rem;

  img {
    width: 100%;
    line-height: 1;
    display: block;
  }

  &__item {
    & + & {
      margin-top: 2.5rem;
    }

    &-title {
      display: flex;
      justify-content: space-between;
      align-items: center;

      button.text-icon {
        .icon-button {
          @extend .icon-edit;
          position: relative;
          top: 0.1rem;
          background-size: auto 100%;
        }
      }
    }

    &-text {
      line-height: 1.65625;
      margin-top: 1rem;

      span {
        @extend .font-size-16;
      }

      p {
        @extend .font-size-14;
        color: $gray-9;
        margin-top: 1rem;
      }
    }

    &-box {
      border: 1px solid $border-color-d;
      border-radius: 1rem;
      padding: 1.5rem 1.5rem 2.5rem;
      margin-top: 1.2rem;
    }

    &-image {
      .no-image {
        height: 15rem;
      }
    }
  }
}

/*   -  */
.diet-statistics {
  margin-top: 2rem;

  .is-only & {
    margin-top: 0;
  }

  .warning {
    color: $red;
  }

  &__selector {
    p {
      @extend .font-size-16;
      line-height: 1.65625;
      color: $gray-6;
    }
    &-buttons {
      display: flex;
      margin-top: 2rem;

      button {
        @extend .font-size-16;
        font-weight: 600;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 5.6rem;
        height: 5.6rem;
        margin-right: 1rem;
        border: 1px solid $secondary-green;
        border-radius: 1.1rem;
        background: $white;
        color: $secondary-green;

        &.active {
          background: $secondary-green;
          color: $white;
        }

        &.inactive {
          border-color: $border-color-a;
          color: $gray-9;
          font-weight: 400;
        }

        &:disabled {
          border-color: $border-color-d;
          border-style: dashed;
          color: $gray-b;
        }
      }
    }
  }

  &__data {
    &-title {
      display: flex;
      width: 100%;
      margin-top: 2.5rem;
      justify-content: space-between;
      align-items: center;

      em {
        @extend .font-size-14;
        color: $gray-9;
      }

      .is-only & {
        margin-top: 0.4rem;
      }
    }

    &-list {
      margin-top: 1.2rem;

      ul {
        display: flex;
        width: 100%;
      }

      li {
        width: 38.56%;

        &:nth-of-type(2) {
          width: auto;
          flex: 1 0 auto;
        }
      }

      dt, dd {
        border: solid $border-color-d;
        border-width: 1px 0;
        text-align: center;
      }

      dt {
        background: $bg-color-f;
        font-size: 1.4rem;
        padding: 0.5rem 0;
        color: $gray-9;
        line-height: 1.4285;
      }

      dd {
        margin-top: -1px;
        font-size: 1.6rem;
        padding: 1.6rem 0 1.5rem;
        line-height: 1.65625;

        ul {
          & + ul {
            margin-top: 1.2rem;
          }

          &:nth-of-type(1) {
            li:first-child {
              color: $gray-6;

              &::before {
                display: none;
              }
            }
          }
        }

        li {
          &:first-child {
            text-align: left;
            color: $gray-9;
            padding-left: 1.5rem;

            &::before {
              content: '- ';
            }
          }
        }
      }
    }

    &-guide {
      margin-top: 1rem;

      li {
        line-height: 1.6;
        word-break: keep-all;
        position: relative;
        font-size: 1.4rem;
        padding-left: 1.4rem;
        color: $gray-9;

        &::before {
          content: '*';
          position: absolute;
          left: 0;
          top: 0;
        }

        & + li {
          margin-top: 1rem;
        }
      }
    }
  }

  .page-diet__top-date + & {
    margin-top: 4rem;
  }
}

/*     */
@keyframes openIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.diet-input-popup {
  background: rgba($black, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: $popup-z-index;
  padding: 0;
  display: none;
  opacity: 0;
  transition: all $base-duration;

  &.active {
    display: block;
    opacity: 1;
  }

  &__outer {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 2.5rem  0 4.5rem;
  }

  &__inner {
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    height: 100%;
    height: calc(100% - constant(safe-area-inset-bottom));
    height: calc(100% - env(safe-area-inset-bottom));
  }

  &__title {
    text-align: center;
    color: $white;
    line-height: 1.66;

    h1 {
      @extend .kb-font;
      @extend .font-size-20;
      font-weight: bold;
    }
    p {
      @extend .font-size-16;
      margin-top: 1rem;
    }
  }

  &__image {
    width: 26.9rem;
    margin: 0 auto;

    img {
      max-width: 100%;
    }

  }

  &__guide {
    padding: 0 1.6rem;
    max-width: 32.8rem;
    margin: 4rem auto 3.1rem;

    ul {
      margin-top: 4rem;
    }

    li {
      @extend .font-size-16;

      position: relative;
      line-height: 1.66;
      color: $white;
      padding-left: 2.0rem;
      word-break: keep-all;

      > span {
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }

  &__buttons {
    position: absolute;
    left: 0;
    width: 100%;
    bottom: 0;
    display: flex;

    button {
      @extend .font-size-16;

      color: $white;
      border-top: 1px solid #8e9090;
      width: 50%;
      height: 4.5rem;
      height: calc(4.5rem + constant(safe-area-inset-bottom)) !important;
      height: calc(4.5rem + env(safe-area-inset-bottom)) !important;
      padding-bottom: calc(constant(safe-area-inset-bottom) - 0.8rem);
      padding-bottom: calc(env(safe-area-inset-bottom) - 0.8rem);

      & + button {
        border-left: 1px solid #8e9090;
      }
    }
  }
}

/*   */
.modal-calender {
  &__container {
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    max-width: 28.7rem;
    height: 14.5rem;
    margin: 0 auto;

    &::before,
    &::after {
      content: '';
      position: absolute;
      left: 0;
      width: 100%;
      height: 2.7rem;
      object-fit: contain;
      z-index: 2;
    }

    &::before {
      top: 0;
      background-image: linear-gradient(to bottom, $white, rgba($white, 0));
    }

    &::after {
      bottom: 0;
      background-image: linear-gradient(to top, $white, rgba($white, 0));
    }
  }
  .calender-swiper {
    flex: 1 0 auto;
  }
}
.calender-swiper {
  margin: -1rem 0;

  .swiper {
    &-container {
      width: 8.75rem;
      height: 16.5rem;
      position: relative;

      &::before, &::after {
        content: '';
        position: absolute;
        left: 0;
        width: 100%;
        height: 1px;
        background: $black;
        z-index: 10;
      }

      &::before {
        top: 5.5rem;
      }

      &::after {
        bottom: 5.5rem;
      }
    }

    &-slide {
      font-size: 1.6rem;
      text-align: center;
      color: $gray-9;
      background: #fff;
      height: 5.5rem;

      /* Center slide text vertically */
      display: -webkit-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      -webkit-justify-content: center;
      justify-content: center;
      -webkit-box-align: center;
      -ms-flex-align: center;
      -webkit-align-items: center;
      align-items: center;
    }

    &-slide-active {
      font-size: 2rem;
      color: $black;
    }
  }
}
