@charset "utf-8";

// 
.button {
  &__flex-wrap {
    display: flex;

    button {
      flex-grow: 1;
    }
  }

  &__wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 3rem;

    button {
      min-width: 7rem;
      margin: 0 0.6rem;
    }
  }
}
button {
  background: transparent;
  &.block {
    @extend .font-size-20;
    display: block;
    width: 100%;
    height: 5.6rem;
    background-color: $brand-y;
    color: $black;
    white-space: nowrap;

    &.gray {
      background-color: $gray-d;
    }

    &.medium {
      height: 4.4rem;
      @extend .font-size-16;
    }

    &.small {
      height: 3.6rem;
      @extend .font-size-14;
    }
    &.inline {
      display: inline-block;
      width: auto;

      &.medium {
        padding: 0 2rem;
      }

      &.small {
        padding: 0 1.5rem;
      }
    }

    &:disabled {
      background-color: $bg-color-f;
    }

    &.bottom-fixed {
    }
  }
  &.text {
    display: inline-block;
    text-decoration: underline;
    @extend .font-size-20;
    color: $black;
    &.medium {
      @extend .font-size-18;
    }
    &.small-01 {
      @extend .font-size-16;
    }
    &.small-02 {
      @extend .font-size-14;
    }
  }
  &.text-icon {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    @extend .font-size-20;
    color: $black;
    i {
      margin-right: 1rem;
      @extend .icon-20;
    }
    i.icon-button {
      @include svgIconColor(button, '000');
    }
    &:disabled i.icon-button {
      @include svgIconColor(button, 'bbb');
    }
    &.medium {
      @extend .font-size-18;
      i {
        margin-right: 0.8rem;
        @extend .icon-18;
      }
    }
    &.small-01 {
      @extend .font-size-16;
      i {
        margin-right: 0.6rem;
        @extend .icon-16;
      }
    }
    &.small-02 {
      @extend .font-size-14;

      i {
        margin-right: 0.4rem;
        @extend .icon-14;
      }
    }
  }

  &:disabled {
    color: $gray-b;
  }

  &.none {
    display: none !important;
  }

  &.only-icon {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    min-width: 3rem;
    min-height: 3rem;
  }

  &.gray-9 {
    color: $gray-9;
  }

  &.bottom-fixed {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: $header-z-index;
  }
}
