@charset "utf-8";

.page-auth-email {
  height: 100%;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  padding: 9.9rem 1.6rem 8.6rem;
  line-height: 1.375;

  &__title {
    @extend .kb-font;
    @extend .font-size-30;
    font-weight: bold;
    line-height: 0.67;
    color: $black;
    text-align: center;
    word-break: keep-all;
  }

  &__guide {
    @extend .font-size-16;
    margin-top: 6rem;
    color: $gray-6;

    li {
      position: relative;
      word-break: keep-all;
      padding-left: 0.9rem;
      line-height: 1.6;

      &::before {
        content: '';
        background: $gray-6;
        width: 0.4rem;
        height: 0.4rem;
        border-radius: 100%;
        position: absolute;
        left: 0;
        top: 1rem;
      }

      & + li {
        margin-top: 1.2rem;
      }
    }
  }

  &__form {
    margin-top: 4rem;

    &-buttons {
      margin-top: 4rem;

      button.block:disabled {
        background: #f0f0f0;
      }
    }
  }

  .table-form {}

  .footer-hide & {
    padding-bottom: 1.6rem;
  }
}

.app-auth {
  height: 100%;
  padding: 8rem 0 4rem;
  position: relative;
  line-height: 1.375;

  &__box {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    width: 100%;
    height: 100%;
    margin: 0 auto;
  }

  &__title {
    @extend .kb-font;
    font-size: 2rem;
    font-weight: 500;
    line-height: 1;
    color: $black;
    text-align: center;
    white-space: nowrap;
    letter-spacing: -0.07em;
    margin-bottom: 4.8rem;
  }

  &__other-button {
    position: absolute;
    left: 0;
    bottom: 4rem;
    width: 100%;
    text-align: center;

    button {
      font-size: 1.6rem;
      display: inline-flex;
      line-height: 1;
      justify-content: center;
      align-items: center;
      padding: 1rem 0;

      &::after {
        @extend .icon-circle_up;
        @extend .icon-16;

        content: '';
        margin-left: 1rem;
      }
    }
  }
}

.app-auth-pin {
  max-width: 32rem;
  padding: 0 1.6rem;

  &__numbers {
    position: relative;
    margin: 0 -4rem;
    text-align: center;
    padding: 0 0 6.25rem;

    span {
      font-size: 1.6rem;
      display: inline-block;
      width: 1.9rem;
      height: 1.9rem;
      line-height: 1.9rem;
      border-radius: 100%;
      text-align: center;
      margin: 0 1rem;

      &.on, &.off {
        border-radius: 100%;
        overflow: hidden;
        font-size: 0;
      }

      &.on {
        background: #000;
      }

      &.off {
        background: $gray-b;
      }
    }

    p {
      position: absolute;
      top: 3.9rem;
      left: 0;
      right: 0;
      font-size: 1.6rem;
      line-height: 1.375;
      text-align: center;

      em {
        color: $red;
      }
    }
  }

  &__keys {
    flex: 0 0 1;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    padding-bottom: 6.53rem;
    position: relative;

    ul {
      width: 100%;
      height: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-end;
    }

    li {
      flex: 0 0 33.3%;
      padding: 0.75rem 1.4rem;

      span {
        display: block;
        padding-top: 100%;
        position: relative;
      }
    }

    button {
      font-size: 3.7rem;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      text-align: center;
      background: $white;
      color: $black;
      border-radius: 100%;
      border: 1px solid $gray-9;
      line-height: 1.38;

      &.active {
        background: $brand-y;
        border-color: $brand-y;
        color: $white;
      }

      &.delete {
        font-size: 1.6rem;
      }
    }
  }
}

.app-auth-bio {
  position: relative;
  width: 100%;
  padding: 0 1.6rem;
  text-align: center;

  &__text {
    font-size: 1.6rem;
    margin-bottom: 2.65rem;
    height: 2.35rem;
  }

  &__target {
    @extend .icon-fingerprint;
    @extend .icon-80;
    width: 8.4rem;
    height: 8.4rem;
    margin: 0 auto;

    &.off {
      opacity: 0.4;
    }

    button {
      width: 100%;
      height: 100%;
      font-size: 0;
    }
  }

  &__buttons {
    margin-top: 8rem;
  }
}

.modal-app-auth {
  &__body {
    padding: 0.5rem 0 1rem;
  }

  &__title {
    @extend .font-size-16;
    text-align: left;
    margin-bottom: 1.5rem;
  }

  &__lists {
    li + li {
      margin-top: 1.2rem;
    }
  }

  &__button {
    display: flex;
    align-items: center;
    width: 100%;
    height: 5.4rem;
    border: 1px solid $border-color-d;
    border-radius: 1rem;
    padding: 0 1.5rem;

    span {
      @extend .font-size-16;
      flex: 1 0 0;
      margin-left: 0.75rem;
      text-align: left;
      display: flex;
      justify-content: space-between;
      position: relative;

      &::before,
      &::after {
        content: '';
        position: absolute;
        display: inline-block;
        top: 50%;
        transform: translateY(-50%);
        border-radius: 50%;
        box-sizing: border-box;
        background-color: $white;
      }

      &::before {
        right: 0;
        width: 2.7rem;
        height: 2.7rem;
        border: 1px solid $border-color-a;
        background-color: $white;
      }

      &::after {
        z-index: 1;
        right: 0.8rem;
        width: 1.1rem;
        height: 1.1rem;
      }
    }

    &.active span {
      &::before {
        border: 1px solid $secondary-green;
        background-color: $secondary-green;
      }
    }

    &:disabled {
      i {opacity: 0.5;}

      span {
        &::before {
          border: 1px solid $border-color-d;
          background-color: $border-color-e;
        }

        &::after {
          display: none;
        }
      }
    }
  }
}

.service-agree {
  height: 100%;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  padding: 0 1.6rem 5.6rem;

  &__title {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin-bottom: 2rem;
    line-height: 1.3;
    height: 5.6rem;
    @extend .font-size-20;
    @extend .kb-font;
  }

  &__article {
    & + & {
      margin-top: 2rem;
    }

    &.lined {
      padding-bottom: 2rem;
      border-bottom: 1px solid $border-color-d;
    }
  }

  &__item {
    & + & {
      margin-top: 2rem;
    }

    button {
      @extend .font-size-16;
      position: relative;
      display: flex;
      align-items: center;
      width: 100%;
      text-align: left;
      padding-right: 2.3rem;

      &::after {
        @extend .icon-arrow_right;
        @extend .icon-18;
        position: absolute;
        right: 0;
        top: calc(50% - 0.85rem);
        content: '';
      }
    }

    .input-checkbox.only-check {
      align-items: flex-start;
      span {
        line-height: 1.38;
        word-break: keep-all;
      }

      i {
        margin-top: 0.4rem;
        &::before {
          @extend .icon-check_line;
          @extend .icon-16;
          filter: none;
          opacity: 0.3;
        }

        &::after {
          display: none;
        }
      }

      input:checked + i {
        &::before {
          @extend .icon-check_line;
          @extend .icon-16;
          filter: invert(57%) sepia(95%) saturate(372%) hue-rotate(44deg) brightness(92%) contrast(86%);
          opacity: 1;
        }
      }
    }
  }

  &__sms {
    @extend .font-size-16;

    dt {
      margin-bottom: 1.5rem;
    }

    dd {
      display: flex;
      align-items: center;
      justify-content: flex-start;

      label + label {
        margin-left: 3rem;
      }
    }
  }
}

.pass-agree {
  &__title {
    @extend .font-size-18;
    @extend .kb-font;
    font-weight: bold;
    margin-bottom: 4rem;
    line-height: 1.67;
  }

  &__form {
    margin-top: 4rem;
  }
}
