//   
.icon-alarm {
  position: relative;
  font-size: 0;

  &::before {
    content: '';
    font-size: 0;
    background-image: url('../img/icons/icon_alarm.svg');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    display: block;
    width: 100%;
    height: 100%;
  }

  &.on {
    &::after {
      position: absolute;
      right: 0;
      top: 0;
      content: '';
      display: block;
      width: 40%;
      height: 40%;
      border-radius: 50%;
      background: $brand-y;
    }
  }
}

.icon-check_line {
  filter: invert(64%) sepia(1%) saturate(0%) hue-rotate(331deg) brightness(97%) contrast(83%);
}

.icon-error_internet {
  width: 6.3rem;
  height: 8rem;
}


