@charset "utf-8";

.event_11 {
  &__list {
    .card-item {
      & + .card-item {
        margin-top: 1rem;
      }
      .no-image {
        height: 15rem;
      }
    }
    .list-item {
      &__date {
        + .list-item__date {
          margin-top: 0.5rem;
        }
        &.on {
          color: $brand-y;
        }
      }
    }
  }
}

.event_21 {
  padding: 2rem 1.6rem;
  .board__content {
    margin-top: 2rem;
    min-height: 29rem;
  }
  &__button {
    margin-top: 3rem;
  }
}

.event_22 {
  &__title {
    margin: 0.4rem 0 1.5rem;
    @extend .kb-font;
    font-weight: 700;
    @extend .font-size-18;
  }
  &__icon-title {
    padding-left: 5rem;
    position: relative;
    margin-bottom: 2.5rem;
    min-height: 4rem;
    flex: 0 0 auto;

    &-i {
      position: absolute;
      left: 0;
      top: 0;
      border:1px solid #d3d4d6;
      border-radius: 1rem;
      width: 4rem;
      height: 4rem;
      display: flex;
      justify-content: center;
      align-items: center;

      &::after {
        content: '';
        @extend .icon-gift;
        @extend .icon-20;
      }
    }

    &-t {
      @extend .font-size-16;
      line-height: 2.65rem;
      color: $gray-6;
      word-break: keep-all;
      letter-spacing: -0.01em;
      em {
        color: $brand-y;
      }
    }
  }
  &__sub-title {
    margin-bottom: 1.2rem;
    font-weight: 700;
    @extend .kb-font;
    @extend .font-size-16;
  }
  &__cal {
    $cal: &;
    &-title {
      border-top: 1px solid $border-color-d;
      border-bottom: 1px solid $border-color-d;
      line-height: 2rem;
      padding: 0.5rem 0;
      background-color: $bg-color-f;
      color: $gray-9;
      @extend .font-size-14;
    }
    &-row {
      display: flex;
      justify-content: flex-start;
      align-items: center;
    }
    &-col {
      width: 14.285%;
      text-align: center;
      button {
        width: 100%;
        height: 100%;
        @extend .font-size-16;
        line-height: 1;
        color: $gray-6;
      }
      .cover {
        width: 4rem;
        height: 4rem;
        color: $gray-6;
        display: flex;
        align-items: center;
        @extend .font-size-16;
        justify-content: center;
      }
      &.on {
        .cover {
          color: $brand-y !important;
          background: url('../img/day_check_ok.png') no-repeat center top;
          background-size: cover;
          button{
            color: $brand-y !important;
          }
        }
      }
    }
    &-cont {
      #{$cal}-row {
        padding: 0.7rem 0;
        &:nth-of-type(1) {
          justify-content: flex-end;
        }
        #{$cal}-col {
          &:last-child {
            color: $red;
            button {
              color: $red;
            }
          }
        }
        &:last-of-type {
          #{$cal}-col {
            &:last-child {
              color: $gray-6;
              button {
                color: $gray-6;
              }
            }
            &:nth-of-type(7) {
              color: $red;
              button{
                color: $red;
              }
            }
          }
        }
      }
    }
  }
  &__popup {
    .modal-popup__container {
      padding: 4rem 2rem 3rem;
    }
    &-cont {
      text-align: center;
      p {
        margin-top: 4rem;
        @extend .font-size-20;
        @extend .kb-font;
        font-weight: 700;
        line-height: 3.25rem;
      }
    }
  }
}
