@charset "utf-8";

.page-error {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  text-align: center;
  padding-bottom: 9.5rem;

  &__title {
    @extend .kb-font;
    font-size: 2rem;
    color: $black;
    font-weight: bold;
    margin-top: 4rem;
  }

  &__text {
    @extend .font-size-16;

    margin-top: 1rem;
    color: $gray-6;
    line-height: 1.375;
    letter-spacing: -0.1em;
  }

  &__buttons {
    margin-top: 6rem;
    font-size: 0;

    button {
      margin: 0 0.8rem;
      min-width: 12rem;
    }
  }
}

.page-splash {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 0 3rem;

  &__logo {
    flex: 1 0 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    p {
      @extend .kb-font;

      font-size: 1.8rem;
      line-height: 1.2;
      color: $black;
      margin-bottom: 3rem;
    }

    img {
      width: 11rem;
    }
  }

  &__info {
    padding-bottom: 3rem;
  }

  &__update-text {
    display: block;
    line-height: 1.5;
    color: $gray-9;
    font-size: 1.2rem;
    font-weight: bold;
    text-align: center;
    margin-bottom: 1.5rem;
  }

  &__progress {
    @include progress($bg-color-d, $brand-y, 0.5rem);
  }

  &__version-text {
    display: block;
    font-size: 1.4rem;
    line-height: 1.4285;
    color: $gray-9;
    text-align: center;
    margin-top: 4rem;
  }
}

.page-app-permissions {
  height: 100%;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  padding: 2rem 3rem 5.6rem;

  &__title {
    font-size: 1.6rem;
    line-height: 1.666;
    color: $gray-6;
  }

  &__list {
    margin-top: 4rem;
    padding-bottom: 3.95rem;

    li {
      position: relative;
      padding-left: 2.4rem;

      & + li {
        margin-top: 2.5rem;
      }
    }

    i {
      position: absolute;
      left: 0;
      top: 0.30rem;
    }

    span {
      display: flex;
      align-items: center;
      vertical-align: middle;
      font-size: 1.8rem;
      line-height: 1.22;
    }

    strong {
      font-weight: 700;
      @extend .kb-font;
    }

    p {
      font-size: 1.6rem;
      line-height: 1.38;
      margin-top: 0.5rem;
      color: $gray-9;
    }
  }
}

.page-results-searching {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  height: 100%;
  background: rgba($black, 0.7);
  padding: 0 3rem;

  &__number {
    font-size: 1.2rem;
    font-weight: bold;
    line-height: 1.5;
    margin-bottom: 1.5rem;
    color: $brand-y;
  }

  &__progress {
    @include progress($secondary-silver, $brand-y);
  }

  &__text {
    font-size: 1.6rem;
    line-height: 1.38;
    font-weight: 300;
    margin-top: 2.75rem;
    color: $white;
  }
}

.page-loading {
  position: fixed;
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  background: rgba($black, 0.5);

  &__icon {
    @include loading;
  }
}

/* all-menu */
.all-menu {
  padding: 0 1.6rem 3rem !important;

  &__top {
    background: $secondary-gold2;
    margin: 0 -1.6rem;
    padding: 0 1.6rem;

    ul {
      display: flex;
    }

    li {
      flex: 1 0 0;
    }

    button {
      width: 100%;
      padding: 2.45rem 0 2.3rem;
    }

    span {
      font-size: 1.1rem;
      line-height: 1.48;
      display: block;
      color: $white;
      margin-top: 0.8rem;
    }
  }

  &__nav {
    dl {
      margin-top: 3rem;
    }

    dt, dd {
      border-bottom: 1px solid $border-color-d;

      button {
        font-size: 1.6rem;
        white-space: nowrap;
      }
    }

    dt {
      button {
        @extend .kb-font;
        line-height: 1.69;
        font-weight: bold;
      }

      padding-bottom: 1.5rem;
    }

    dd {
      button {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: 5.35rem;
        padding: 0 1.4rem;
        line-height: 1.66;

        &::after {
          @extend .icon-arrow_right;
          @extend .icon-20;
          content: '';
        }
      }
    }
  }
}

.photo-popup-buttons {
  position: fixed;
  left: 1.6rem;
  right: 1.6rem;
  bottom: 3rem;
  padding-bottom: env(safe-area-inset-bottom);
  padding-bottom: constant(safe-area-inset-bottom);
  z-index: $popup-z-index;
  display: none;

  &.active {
    display: block;
  }

  &__box {
    position: relative;

    button {
      @extend .font-size-16;
      width: 100%;
      text-align: center;
      background: $white;
    }
  }

  &__list {
    border-radius: 1rem;
    margin-bottom: 1rem;
    overflow: hidden;

    button {
      height: 4.5rem;
    }

    li + li {
      border-top: 1px solid $border-color-d;
    }
  }

  &__cancel {
    button {
      height: 4.7rem;
      border-radius: 1rem;
    }
  }
}

/*    */
.blood-method {
  &__image {
    height: 8rem;

    img {
      max-height: 100%;
    }
    .no-image {
      width: 5rem;
    }
  }

  &__box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: $bg-color-f;
    border-radius: 1rem;
    width: 100%;
    padding: 2rem;

    &-text {
      @extend .kb-font;
      font-size: 1.8rem;
      line-height: 1.38;
      margin-top: 1.5rem;
      font-weight: bold;
    }
  }

  &__guide {
    @extend .font-size-16;
    margin-top: 2.5rem;

    &-title {
      @extend .kb-font;
      color: $black;
      word-break: keep-all;
      font-weight: bold;
      line-height: 1.69;
    }

    &-items {
      margin-top: 1.2rem;

      li {
        color: $gray-6;
        position: relative;
        padding-left: 1.6rem;
        word-break: keep-all;
        line-height: 1.66;

        & + li {
          margin-top: 1.2rem;
        }

        span {
          position: absolute;
          left: 0;
          top: 0;
        }

        i {
          position: relative;
          top: -0.1rem;
        }
      }
    }
  }

  &__searching {
    margin-top: 12rem;
    text-align: center;

    &-icon {
      @extend .icon-searching;
      @extend .icon-80;
    }

    &-text {
      margin-top: 4rem;
      line-height: 1.375;
      word-break: keep-all;

      em {
        @extend .kb-font;
        font-size: 2rem;
        font-weight: bold;
        display: block;
        color: $black;
        word-break: keep-all;
      }

      span {
        font-size: 1.6rem;
        display: block;
        margin-top: 1rem;
        color: $gray-9;
        word-break: keep-all;
        line-height: 1.38;
      }
    }
  }

  &__searching &__image {
    &::before {
      width: 8rem;
      height: 8rem;
      line-height: 8rem;
    }
  }
}

// 
.alarm {
  &__sub-text {
    margin-bottom: 1.2rem;
    color: $gray-6;
    line-height: 1.6;
    font-size: 1.6rem;
  }

  .list__wrap .list-item {
    > button {
      padding: 1.5rem 1.5rem 1.5rem 3rem;

      &::before {
        position: absolute;
        left: 1.5rem;
        top: 2.4rem;
        width: 0.8rem;
        height: 0.8rem;
        border-radius: 50%;
        background-color: $gray-b;
        content: '';
      }

      &::after {
        display: none;
      }
    }

    &--on {
      > button {
        &::before {
          background-color: $secondary-green;
        }
      }
    }
  }
}

// 
.term {
  &__inner {
    & + & {
      margin-top: 4rem;
    }
  }

  &__sub-title {
    @extend .kb-font;
    font-size: 1.8rem;
    line-height: 1.11;
    margin-bottom: 1.5rem;
    color: $black;
  }

  &-detail {
    &__section {
      & + & {
        margin-top: 4rem;
      }
    }

    &__select {
      margin-bottom: 2.5rem;
    }

    &__text {
      @extend .font-size-16;
      line-height: 1.66;
      word-break: keep-all;
      letter-spacing: -0.03em;
      color: $gray-6;

      & + & {
        margin-top: 1.2rem;
      }
    }

    &__title {
      @extend .font-size-18;
      margin: 0 0 1.5rem;
      font-weight: 600;
      line-height: 1.33;

      &.mt0 {
        margin-top: 0;
      }
    }

    &__sub-title {
      @extend .font-size-16;
      margin: 2.5rem 0 1.2rem;
      font-weight: 700;
      line-height: 1.38;
    }

    &__button {
      text-align: center;

      &.group {
        margin-top: 3rem;

        button {
          margin-top: 0;

          & + button {
            margin-left: 1rem;
          }
        }
      }

      button {
        margin-top: 1.8rem;
      }
    }
  }
}

// 
.certificate {
  &__card {
    $item: &;
    width: 100%;
    border: 1px solid $border-color-d;
    border-radius: 1rem;
    white-space: nowrap;

    + #{$item} {
      margin-top: 1rem;
    }

    span {
      display: block;
    }

    &-icon {
      padding-right: 1.5rem;
    }

    &-text {
      flex-grow: 1;
    }

    &-code {
      @extend .font-size-14;
      color: $gray-6;
      line-height: 1.61;
    }

    &-name {
      @extend .font-size-16;
      margin-top: 1.5rem;
      color: $black;
      line-height: 1.66;
    }

    &-info {
      @extend .font-size-14;
      display: flex !important;
      justify-content: flex-start;
      align-items: center;
      margin-top: 1rem;
      color: $gray-9;
      line-height: 1.61;

      > * {
        @extend .font-size-14;
        position: relative;
        display: block;
        padding: 0 1rem;

        + * {
          &::before {
            position: absolute;
            top: calc(50% - 0.6rem);
            left: 0;
            width: 1px;
            height: 1.4rem;
            background: $border-color-d;
            content: '';
          }
        }
      }

      em {
        margin-left: -1rem;
      }
    }

    &-del {
      display: flex;
      width: 100%;
      padding: 1rem;
      justify-content: center;
      align-items: center;
      border-top: 1px solid $border-color-d;
      color: $black;
      @extend .font-size-16;

      i {
        margin-right: 0.7rem;
        margin-top: 0.1rem;
      }
    }

    &-inner {
      display: flex;
      width: 100%;
      padding: 1.5rem;
      justify-content: center;
      align-items: center;
      text-align: left;
    }

    &-inner.disabled,
    &-inner:disabled {
      #{$item}-icon {
        opacity: 0.6;
        filter: invert(94%) sepia(0%) saturate(31%) hue-rotate(208deg) brightness(82%) contrast(84%);
      }

      #{$item}-text,
      #{$item}-code,
      #{$item}-name,
      #{$item}-info {
        color: $gray-b;
      }
    }
  }

  &-import {
    &__title {
      @extend .kb-font;
      font-size: 1.8rem;
      font-weight: 700;
      line-height: 1.67;
      margin-bottom: 1.5rem;
      word-break: keep-all;
    }

    &__list {
      li {
        $this: &;
        position: relative;
        padding-left: 1.6rem;
        word-break: keep-all;
        color: $gray-6;
        line-height: 1.66;
        font-size: 1.6rem;

        & + li {
          margin-top: 1.2rem;
        }
      }

      @for $i from 1 through 10 {
        li:nth-of-type(#{$i}) {
          &::before {
            position: absolute;
            left: 0;
            top: 0;
            content: '#{$i}.';
          }
        }
      }
    }

    &__number {
      margin: 4rem 0 3rem;

      dt {
        @extend .kb-font;
        font-size: 1.8rem;
        color: $black;
        font-weight: 700;
        line-height: 1.24;
        margin-bottom: 1.5rem;
      }

      dd {
        display: flex;

        input + input {
          margin-left: 1rem;
        }
      }
    }
  }
}

//  
.switch-list {
  border-top: 1px solid $border-color-d;

  &__item {
    display: flex;
    padding: 1.32rem 1.5rem;
    justify-content: space-between;
    align-items: flex-start;
    border-bottom: 1px solid $border-color-d;

    &-title {
      padding-top: 0.2rem;
      flex-grow: 1;
      color: $black;
      text-align: left;
      line-height: 2.2rem;
      @extend .font-size-16;

      .health-info__section & {
        padding-top: 0.3rem;
      }
    }

    &-sub-title {
      margin-top: 1rem;
      color: $gray-9;
      @extend .font-size-14;
    }

    &-button {
      margin-left: 0.5rem;
      flex-shrink: 0;

      &.width52 {
        text-align: right;
        width: 5.2rem;
      }

      &-and {
        margin-top: 1.5rem;
      }

      .health-info__section & {
        margin-top: 0.3rem;
      }
    }

    &-icon {
      width: 3.2rem;
      height: 3.2rem;
      overflow: hidden;
      border-radius: 1rem;
      margin-right: 1rem;
      flex-shrink: 0;

      img {
        width: 100%;
      }
    }
  }
}

//  
.health-info {
  &__info {
    font-size: 1.6rem;
    margin-bottom: 2rem;
    color: $gray-6;
    line-height: 1.66;
    word-break: keep-all;
  }

  &__sub-title {
    @extend .kb-font;
    font-size: 1.8rem;
    font-weight: bold;
    color: $black;
    line-height: 1.11;
    margin: 0 0 1.5rem;
  }

  &__section {
    margin-top: 4rem;
  }

  &__no-device {
    margin-top: 2.5rem;
    text-align: right;
    @extend .font-size-14;

    i {
      margin-right: 0.3rem;
      margin-bottom: 0.3rem;
    }
  }
}

// 
.withdraw {
  &__sub-title {
    @extend .kb-font;
    margin-bottom: 1.5rem;
    font-size: 1.8rem;
    font-weight: bold;
    line-height: 1.11;
    color: $black;
  }

  &__form-section {
    margin-top: 1.5rem;
  }
}

// 
.setting-nav {
  width: 100%;

  li {
    border-bottom: 1px solid $border-color-d;

    &:nth-of-type(1) {
      border-top: 1px solid $border-color-d;
    }

    > button {
      @extend .font-size-16;
      position: relative;
      width: 100%;
      height: 5.35rem;
      padding: 0 $input-padding;
      text-align: left;

      &::after {
        position: absolute;
        right: 1rem;
        top: calc(50% - 0.7rem);
        @extend .icon-arrow_right;
        @extend .icon-16;
        content: '';
      }
    }

    &.accordion dl {
      border: none;
    }
  }
}

.app-version {
  width: 100%;
  text-align: center;
  margin: 0;
  color: $gray-9;
  @extend .font-size-14;
}

// 
.my-info {
  &__profile {
    text-align: center;
    margin-bottom: 1.5rem;

    &-img {
      margin-bottom: 1rem;

      button {
        position: relative;
        display: block;
        width: 8rem;
        height: 8rem;
        margin: 0 auto;
        overflow: hidden;
        border-radius: 50%;
        background: $bg-color-f;
        text-align: center;
      }

      img {
        position: relative;
        width: 100%;
        height: 100%;
        z-index: 1;
      }

      i {
        position: absolute;
        top: 0.8rem;
        left: calc(50% - 2rem);
        z-index: 1;
        filter: invert(78%) sepia(7%) saturate(24%) hue-rotate(0deg) brightness(97%) contrast(87%);
      }

      &-edit {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 2.6rem;
        line-height: 2.6rem;
        background: $brand-y;
        color: $black;
        z-index: 2;
        @extend .font-size-14;
      }
    }

    &-text {
      @extend .font-size-18;
      color: $black;
      line-height: 1.33;

      strong {
        vertical-align: top;
        font-weight: 700;
      }
    }
  }

  &__item {
    border-bottom: 1px solid $border-color-d;
    @extend .font-size-16;
    color: $black;

    &-inner {
      padding: 1.5rem;
    }

    dl {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      line-height: 1.38;

      dt {
        width: 9rem;
        flex-shrink: 0;
      }

      dd {
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        flex-direction: column;
        color: $gray-6;
        text-align: right;

        button {
          width: auto;
        }

        p {
          margin-top: 1.5rem;
        }
      }
    }

    button {
      width: 100%;
      text-align: left;
      @extend .font-size-16;
      color: $black;

      &.red {
        color: $red;
      }
    }
  }
}
