@charset "utf-8";

html, body {
  background: $white;
  //overflow: hidden;
  position: relative;
  touch-action: none;
  -ms-touch-action: none;
}

html {
  font-family: 'Noto Sans KR';

  img {
    max-width: 100%;
  }

  &.outer-overflow {
    overflow: hidden;

    body, .wrapper {
      overflow: hidden;
    }
  }
}

.wrapper {
  position: relative;
  //overflow: hidden;

  padding: {
    top: env(safe-area-inset-top);
    bottom: env(safe-area-inset-bottom);
    right: env(safe-area-inset-right);
    left: env(safe-area-inset-left);
    top: constant(safe-area-inset-top);
    bottom: constant(safe-area-inset-bottom);
    right: constant(safe-area-inset-right);
    left: constant(safe-area-inset-left);
  }

  &.footer-hide {
    .bottom-fixed,
    .footer {
      bottom: -10rem;
    }

    .footer__button-nav {
      bottom: -15rem;
    }

    .content-wrap {
      padding-bottom: 0;
    }
  }
}

.header, .footer {
  position: absolute;
  left: 0;
  width: 100%;
  z-index: $header-z-index;
  transition: all $base-duration;
}

.header {
  top: 0;
  top: constant(safe-area-inset-top);
  top: env(safe-area-inset-top);
  z-index: $header-z-index + 5;
  padding: {
    right: env(safe-area-inset-right);
    left: env(safe-area-inset-left);
    right: constant(safe-area-inset-right);
    left: constant(safe-area-inset-left);
  }
}

.footer {
  bottom: 0;
  padding: {
    bottom: constant(safe-area-inset-bottom);
    bottom: env(safe-area-inset-bottom);
    right: env(safe-area-inset-right);
    left: env(safe-area-inset-left);
    right: constant(safe-area-inset-right);
    left: constant(safe-area-inset-left);
  }

  &__nav {
    background: $bg-color-f;
    border-top: 1px solid $border-color-e;
  }
  &__button-nav {
    background: $bg-color-f;
    padding: 0;
    nav {
      padding: {
        right: env(safe-area-inset-right);
        left: env(safe-area-inset-left);
        right: constant(safe-area-inset-right);
        left: constant(safe-area-inset-left);
      }
      border-top: 1px solid $border-color-e;
    }
  }
}

.content-wrap {
  padding: 5.6rem 0 6rem;
  padding: {
    bottom: calc(constant(safe-area-inset-bottom) + 6rem);
    bottom: calc(env(safe-area-inset-bottom) + 6rem);
  }
  height: 100%;

  &.header-large {
    padding-top: 9.1rem;
  }

  &.footer-none {
    padding-bottom: 0;
  }

  &.footer-button {
    padding-bottom: 11.7rem;
  }

  &.bottom-button-fixed {
    padding-bottom: 5.6rem;
  }

  .scrollBox {
    -webkit-overflow-scrolling: touch;
    height: 100%;
    padding: 2rem 1.6rem 3rem;
    padding: {
      bottom: calc(constant(safe-area-inset-bottom) + 3rem);
      bottom: calc(env(safe-area-inset-bottom) + 3rem);
    }

    &.flex-height {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;
    }

    &.no-padding {
      padding: 0;
    }
  }
}

header {
  background-color: $white;
  padding: 0 1.6rem;

  .logo {
    width: 5.1rem;

    img {
      width: 100%;
    }
  }

  .title {
    flex-grow: 1;
    text-align: center;
    line-height: 3rem;
    @extend .font-size-20;
    @extend .kb-font;
  }

  .right-icons {
    font-size: 0;
    margin-right: -0.5rem;

    button + button {
      margin-left: 1.3rem;

      &.narrow {
        margin-left: 0.5rem;
      }
    }
  }

  .header {
    button {
      position: relative;
    }

    &__logo {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 5.6rem;
      padding: 1.3rem 0;
    }

    &__back {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 5.6rem;
      padding: 1.3rem 0;

      .back {
        margin-left: -0.9rem;
      }

      .title {
        padding-left: 1.2rem;
        text-align: left;
        margin-top: 0.25rem;
      }
    }

    &__title {
      position: relative;
      height: 5.6rem;
      padding: 1.5rem 0;

      &--close {
        position: absolute;
        right: -0.5rem;
        top: calc(50% - 1.5rem);
      }
    }

    &__close {
      height: 5.6rem;
      padding: 1.3rem 0;
      margin: 0 -0.5rem;
      text-align: right;
    }
  }
}

nav {
  width: 100%;

  ul {
    display: flex;
    height: 6rem;

    li {
      width: 25%;
      text-align: center;

      button {
        display: block;
        width: 100%;
        padding: 0.8rem 0 0.72rem;
        line-height: 2.2rem;
        background-color: $bg-color-f;
        padding: {
          top: calc(constant(safe-area-inset-bottom)/2 + 0.8rem);
          top:  calc(env(safe-area-inset-bottom)/2 + 0.8rem);
          bottom: calc(constant(safe-area-inset-bottom)/2 + 0.72rem);
          bottom: calc(env(safe-area-inset-bottom)/2 + 0.72rem);
        }
      }

      span {
        @extend .font-size-12;
        display: block;
      }

      i {
        margin-bottom: 0.6rem;
      }

      &.on {
        button {
          position: relative;
          background-color: $white;

          &::before {
            position: absolute;
            left: 0;
            top: -2px;
            z-index: 1;
            width: 100%;
            height: 2px;
            content: '';
            background: $brand-y;
          }
        }
      }
    }
  }
}

.contents-padding {
  padding: 1.6rem 1.6rem 3rem;
  padding: {
    bottom: calc(constant(safe-area-inset-bottom) + 3rem);
    bottom: calc(env(safe-area-inset-bottom) + 3rem);
  }
}

// fix bottom
.bottom-fixed {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: $header-z-index;
  background: $white;
  transition: all $base-duration;
  height: calc(5.6rem + constant(safe-area-inset-bottom)) !important;
  height: calc(5.6rem + env(safe-area-inset-bottom)) !important;

  button {
    height: 100% !important;
    padding: {
      bottom: calc(constant(safe-area-inset-bottom) / 2);
      bottom: calc(env(safe-area-inset-bottom) / 2);
    }
  }
}

.wrapper-full-popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  transition: left $base-duration;
  z-index: $popup-z-index + 1;
  display: none;

  &.left-padding {
    display: block;
    padding-left: 3rem;
    left: 100%;

    &.active {
      left: 0;
    }
  }

  &.active {
    display: block;
  }

  &__inner {
    position: relative;
    height: 100%;
    background: $white;
    padding: {
      top: calc(constant(safe-area-inset-bottom) * 2);
      top: calc(env(safe-area-inset-bottom) * 2);
    }
  }
}