@charset "utf-8";
// 
.point-info {
  &__popup {
    dl {
      + dl {
        margin-top: 4rem;
      }
      dt {
        margin-bottom: 1.5rem;
        color: $black;
        font-weight: 700;
        @extend .font-size-18;
      }
      dd {
        position: relative;
        padding-left: 0.9rem;
        + dd {
          margin-top: 1.2rem;
        }
        color: $gray-6;
        @extend .font-size-16;
        &::before {
          position: absolute;
          content: '';
          left: 0;
          top: 1.1rem;
          width: 0.4rem;
          height: 0.4rem;
          border-radius: 50%;
          background-color: $gray-6;
        }
      }
    }
  }
}

.mdp65 {
  .term-detail {
    &__title{
      font-weight: 700;
      margin-bottom: 1.5rem;
    }
    &__sub-title {
      margin-bottom: 1.2rem;
    }
  }
}

.point-change {
  &-title {
    padding-left: 5rem;
    position: relative;
    margin: 0.4rem 0 2rem;
    min-height: 4rem;
    flex: 0 0 auto;
  
    &__icon {
      position: absolute;
      left: 0;
      top: 0;
      border:1px solid #d3d4d6;
      border-radius: 1rem;
      width: 4rem;
      height: 4rem;
      display: flex;
      justify-content: center;
      align-items: center;
  
      &::after {
        content: '';
        @extend .icon-achievement;
        @extend .icon-20;
      }
    }
  
    &__text {
      @extend .font-size-16;
      line-height: 1.65625;
      color: $gray-6;
      word-break: keep-all;
  
      strong {
        font-weight: 600;
        color: $black;
        vertical-align: top;
      }
    }
  }
  &-agree {
    &__all {
      padding: 2rem 0;
      border-bottom: 1px solid $border-color-d;
      .input-checkbox {
        span {
          font-weight: 700;
        }
      }
    }
    &__list {
      padding: 1rem 0;
    }
    &__item {
      button {
        position: relative;
        display: block;
        width: 100%;
        padding: 1rem 0;
        text-align: left;
        @extend .font-size-16;
        &::after {
          position: absolute;
          right: 1rem;
          top: 1.4rem;
          @extend .icon-arrow_right;
          @extend .icon-18;
          content: '';
        }
        span {
          color: $gray-6;
          em {
            display: inline-block;
            margin-top: 0.5rem;
            color: $gray-9;
          }
        }
        .input-checkbox {
          align-items: flex-start;
        }
      }
    }
  }
  &-tab {
    height: 100%;
    .tab {
      &__item {
        span {
          i {
            margin-left: 0.6rem;
          }
          em {
            display: block;
            margin-top: 0.5rem;
          }
        }
        &--on {
          em {
            font-weight: 700;
          }
        }
      }
    }
    &__wrap {
      padding: 3rem 1.6rem 7rem;
    }
  }
  &-article {
    margin-bottom: 2.5rem;
    &.mt30 {
      margin-top: 3rem;
    }
  }
  &-check {
    margin-top: 1.5rem;
    text-align: right;
  }
  &-detail {
    li {
      position: relative;
      @extend .font-size-14;
      padding-left: 1rem;
      color: $gray-9;
      line-height: 2.5rem;
      + li {
        margin-top: 1rem;
      }
      &::before {
        position:absolute;
        left: 0;
        top:0;
        content: '-';
      }
    }
  }
}

.point-gray-bg-box {
  padding: 2rem 1.5rem;
  color: $white;
  background: $bg-color-77;
  border-radius: $input-radius;
  &__flex-sb {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__center {
    text-align: center;
    @extend .kb-font;
    @extend .font-size-18;
    line-height: 2.2rem;
    i {
      margin: -0.5rem 0.8rem 0;
      @include iconFilter;
    }
  }
}
.point-change-both {
  @extend .font-size-14;
  .point {
    flex-grow: 1;
    text-align: right;
    line-height: 3rem;
    @extend .font-size-18;
    @extend .kb-font;
  }
}

//  
.point-end {
  padding:4rem 0 2rem;
  dl {
    display: flex;
    justify-content: space-between;
    align-items: center;
    dt {
      white-space: nowrap;
      min-width: 6rem;
      @extend .font-size-14;
      color: $gray-6;
      line-height: 2.25rem;
    }
    dd {
      margin-left: 1.5rem;
      flex-grow: 1;
      text-align: right;
      @extend .font-size-14;
      color: $black;
      line-height: 2.65rem;
    }
  }
  &__title {
    margin-bottom: 0.5rem;
    dl {
      dd {
        @extend .font-size-25;
        font-weight: 700;
        line-height: 1.5em;
      }
    }
  }
  &__list {
    dl {
      padding: 1.5rem 0;
      border-bottom: 1px solid $border-color-d;
    }
  }
  &__text {
    margin-top: 2.5rem;
    color: $black;
    @extend .font-size-16;
    line-height: 2.65rem;
  }
}
.point-prod {
  margin-top: 2rem;
  &__point {
    font-size: 3.5rem;
    line-height: 0.86;
  }
  &__top {
    margin-bottom: 4rem !important;
  }

  &-purchase {
    &__slide {
      display: none;
      position: absolute;
      bottom: 11.7rem;
      left: 0;
      width: 100%;
      padding: 1.6rem;
      background: $white;
      z-index: $header-z-index + 10;
      &.on {
        display: block;
      }
      dl {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 1.5rem 0;
        dt {
          @extend .font-size-14;
          color: $gray-6;
          line-height: 2.25rem;
          word-break: keep-all;
        }
        dd {
          flex-shrink: 0;
          margin-left: 1.5rem;
          white-space: nowrap;
          text-align: right;
          @extend .font-size-14;
          color: $black;
          line-height: 2.65rem;
          strong {
            font-weight: 700;
          }
          &.last {
            @extend .font-size-20;
          }
        }
      }
      button {
        width: 100%;
        text-align: center;
      }
    }
    &__more-bg {
      display: none;
      position: absolute;
      bottom: 11.7rem;
      width: 100%;
      height: 100%;
      background: rgba($black, 0.7);
      z-index: $header-z-index + 9;
      &.on {
        display: block;
      }
    }
  }
}

//tip
.tip-title {
  position: relative;
  padding-top: 1.2rem;
  &__tip {
    position: absolute;
    top: 0;
    left: 1rem;
    padding: 0.1rem 1rem;
    @extend .font-size-14;
    background: $brand-y;
    border-radius: 0.5rem;
    line-height: 1.61;
    &::after {
      position: absolute;
      content: '';
      top: 100%;
      left: 1rem;
      width: 0px;
      height: 0px;
      border-bottom: 1.2rem solid transparent;
      border-left: 1.2rem solid $brand-y;
    }
  }
  &__title {
    padding: 1.5rem;
    text-align: center;
    color: $black;
    @extend .font-size-18;
    background-color: $bg-color-f;
    border-radius: 1rem;
    &.strong {
      @extend .kb-font;
      padding: 2.7rem 1.5rem;
    }
    .gray {
      color: rgba($black, 0.64);
      font-family: inherit;
    }
    strong {
      font-weight: 700;
      vertical-align: top;
      font-family: inherit;
    }
  }
}

.mdp_72 {
  .tip-title {
    margin-top: 0.4rem;
    margin-bottom: 6rem;
  }
  &__icon {
    text-align: center;
    i {
      margin-bottom: 4rem;
    }
    &-title {
      margin-bottom: 1rem;
      @extend .kb-font;
      @extend .font-size-20;
    }
    &-content {
      color: $gray-9;
      line-height: 1.66;
      @extend .font-size-16;
    }
  }
  &__popup {
    color: $gray-6;
    @extend .font-size-16;
    &-title {
      margin-bottom: 1.5rem;
    }
    &-button {
      button {
        width: 100%;
        padding: 1rem 2rem;
        text-align: center;
        color: $gray-6;
        @extend .font-size-16;
        border:1px solid $border-color-a;
      }
      li {
        margin-bottom: 1.5rem;
      }
    }
  }
}

.page-mdp-73 {
  .tip-title {
    margin-top: 0.4rem;
    margin-bottom: 4rem;
  }
  .manage-icon-box,
  .manage-gray-box {
    margin-bottom: 4rem;
  }
  .manage-h3 {
    margin-top: 4rem;
    margin-bottom: 1.5rem;
  }
}

.page-mdp-78 {
  .manage-gray-box {
    margin-top: 0.4rem;
    margin-bottom: 4rem;
  }
  &__title {
    margin-bottom: 4rem;
  }
  .manage-h4,
  .manage-text {
    margin-bottom: 1.2rem;
  }
  .tip-title {
    margin-bottom: 2.5rem;
  }
  &__list {
    margin-bottom: 4rem;
  }
  &__sub-title {
    margin-bottom: 1.5rem;
  }
  .list__wrap {
    margin-bottom: 2.5rem;
  }
  &__button {
    text-align: center;
  }
}

.page-mdp-721 {
  th {
    white-space: nowrap;
  }
}


/// 
.manage {
  &-icon-box {
    @include iconBoxTitle(icon-achievement);
    &__box {
      margin-top: 1rem;
    }
  }
  &-gray-box {
    padding: 2.4rem;
    color: $white;
    background: $bg-color-77;
    border-radius: $input-radius;
    button {
      width: 100%;
      color: $white;
      text-align: left;
    }
    &.ptb30 {
      padding: 3rem 2.4rem;
    }
    &.pd20{
      padding: 2rem;
    }
    &.pd15 {
      padding: 1.5rem;
    }
    .title {
      margin-bottom: 1rem;
      font-size: 2rem;
      font-weight: 700;
      @extend .kb-font;
      i {
        margin-bottom: 0.2rem
      }
      * {
        font-family: inherit;
      }
    }
    .middle-title {
      @extend .kb-font;
      font-size: 1.8rem;
      * {
        font-family: inherit;
      }
      b {
        vertical-align: baseline;
      }
    }
    .sub-title {
      margin-bottom: 1.5rem;
      font-size: 1.6rem;
    }
    .small {
      font-size: 1.4rem;
    }
    i {
      @include iconFilter;
    }
    .flex-sb {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .big-text {
      @extend .kb-font;
      font-size: 3.5rem;
      line-height: normal;
    }
    .both-box {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin: 0 -2.3rem;
      &.center {
        text-align: center;
      }
      dl {
        width: 50%;
        margin: 0 2.3rem;
        dt {
          font-size: 1.4rem;
          margin-bottom: 1rem;
        }
        dd {
          font-size: 2.5rem;
          @extend .kb-font;
          * {
            font-family: inherit;
            vertical-align: top;
          }
          .divider {
            margin: 0 0.5rem;
          }
        }
        + dl {
          position: relative;
          &::before {
            position: absolute;
            left: -2.3rem;
            top: calc(50% - 2.25rem);
            content: '';
            width: 1px; 
            height: 4.5rem;
            opacity: 0.5;
            background-color: #ffffff;
          }
        }
      }
    }
    .both-list {
      dl {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 1.5rem 0;
        &:first-of-type {
          padding-top: 0.5rem;
        }
        &:last-of-type {
          padding-bottom: 0.5rem;
        }
        + dl {
          border-top: 1px solid rgba($white, 0.5);
        }
        dt {
          font-size: 1.4rem;
          line-height: 1.07;
        }
        dd {
          font-size: 1.8rem;
          @extend .kb-font;
          * {
            font-family: inherit;
            vertical-align: top;
          }
          .divider {
            margin: 0 0.5rem;
          }
        }
      }
    }

    .no-data {
      margin: 2.1rem 0;
      font-size: 1.6rem;
      text-align: center;
    }

    &.type2 {
      background: $secondary-silver;
    }
  }

  &-light-box {
    @extend .kb-font;
    border-radius: 1rem;
    background: $bg-color-f;
    padding: 2rem;
    color: $black;
    font-size: 1.8rem;
    display: flex;
    align-items: baseline;
    justify-content: center;

    b {
      font-weight: 700;
    }
  }

  &-h3 {
    @extend .kb-font;
    font-size: 1.8rem;
    line-height: 1.67;
    color: $black;

    strong {
      font-family: inherit;
      font-weight: 700;
      vertical-align: top;
    }
    .icon {
      margin-left: 0.8rem;
      margin-bottom: 0.3rem;
    }
    &.with-tag {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
    }
  }
  &-h4 {
    @extend .kb-font;
    font-size: 1.6rem;
    line-height: 1.66;
    color: $black;
    strong {
      font-family: inherit;
      font-weight: 700;
      vertical-align: top;
    }
    &.with-tag {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .tooltip-icon {
      margin: -0.5rem;
      vertical-align: middle;
    }
  }

  &-text {
    font-size: 1.6rem;
    line-height: 1.66;
    color: $gray-6;
    word-break: keep-all;
    a {
      color: $gray-6;
    }

    &.black {
      color: $black;
    }
    strong {
      vertical-align: top;
      font-weight: 700;
    }
    .red {
      color: $red;
    }
    .blue {
      color: $blue;
    }
  }
  &-sub-text {
    font-size: 1.4rem;
    line-height: 1.61;
    color: $gray-9;
    word-break: keep-all;
    strong {
      vertical-align: top;
      font-weight: 700;
    }
    &.line-height-14 {
      line-height: 1.43;
    }
  }
  &-super-text {
    text-align: right;
    color: $gray-6;
    font-size: 1.2rem;
    &.black {
      color: $black;
    }
  }

  &-graph {
    padding: 1rem 0 2rem;
    overflow: hidden;

    &__box {
      @extend .font-size-12;
      position: relative;

      .show-number & {
        margin-left: 4.225rem;

        &.small {
          margin-left: 2.4rem;
        }
      }
    }

    &__label {
      text-align: right;
      line-height: 1.48;

      span {
        margin-right: 1.5rem;
      }

      em {
        margin-left: 1rem;
        vertical-align: baseline;
      }

      label {
        & + label {
          margin-left: 1rem;
        }

        &::before {
          content: '';
          width: 0.75rem;
          height: 0.75rem;
          display: inline-block;
          border-radius: 100%;
          margin-right: 0.3rem;
        }

        &:nth-of-type(1)::before {
          background: $secondary-green;
        }

        &:nth-of-type(2)::before {
          background: $brand-y;
        }

        &.green::before {
          background: $secondary-green;
        }

        &.yellow::before {
          background: $brand-y;
        }

        &.red::before {
          background: $red;
        }

        &.blue::before {
          background: $blue;
        }

        &.gray::before {
          background: $gray-6;
        }
      }
    }

    &__grid {
      margin-top: 1rem;

      li {
        position: relative;
        line-height: 0;
        font-weight: 200;
        font-size: 0;
        border-bottom: 1px dotted #e6e6e8;
        &:last-child {
          border-bottom-style: solid;
          border-bottom-color: $border-color-d;
        }
        & + li {
          margin-top: 2.5rem;
        }

        span {
          position: absolute;
          right: 100%;
          margin-right: 1rem;
          font-size: 1.2rem;
          white-space: nowrap;
          font-weight: bold;
          color: $gray-6;
        }
      }

      &.small li + li {
        margin-top: 1.8875rem;
      }
      &.bbnone {
        li {
          border-bottom: none;
        }
      }
    }

    &__table,
    &__table-dot {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 100%;

      ul {
        position: relative;
        display: flex;
        height: 100%;
      }

      li {
        position: relative;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        color: $gray-6;

        > span {
          position: absolute;
          left: -100px;
          right: -100px;
          bottom: -2rem;
          text-align: center;

          &.bold {
            color: $black;
            font-weight: bold;
          }
        }

        em {
          position: absolute;
          bottom: 100%;
          white-space: nowrap;
          font-style: normal;
          margin-bottom: 0.5rem;
          font-weight: 500;
        }
      }
    }

    &__table {
      ul {
        justify-content: space-around;
      }

      li {
        flex: 0 0 1;
        text-align: center;

        div {
          display: block;
          width: 0.8rem;
          margin: 0;
          position: relative;

          &.full-bar {
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;

            > span {
              display: block;
              width: 100%;
            }
          }

          &.both {
            display: flex;
            width: 4.6rem;
            height: 100%;
            align-items: flex-end;
            justify-content: space-between;
          }
        }

        em {
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }

    &__table-dot {
      padding: 0 2.15rem;

      ul {
        justify-content: space-between;
      }

      li {
        div {
          position: relative;
          width: 0;
          height: 0;
        }

        i {
          display: block;
          width: 0.8rem;
          height: 0.8rem;
          margin: -0.4rem 0 0 -0.4rem;
          border-radius: 100%;
          line-height: 0;
        }

        em {
          left: 100%;
        }

        &.width-line {
          position: absolute;
          height: 1px;
          width: 100%;
          left: 0;
        }
        &.height-line {

        }
      }
    }

    &__line {
      position: absolute;
      width: 100%;
      text-align: right;
      line-height: 0;
      border-top: 1px dotted;

      p {
        position: relative;
      }

      span {
        position: absolute;
        bottom: 0.2rem;
        right: 0;
        line-height: 1;
        font-weight: bold;
      }

      &.red-dotted-line {
        border-color: $red;
        color: $red;
      }

      &.blue-dotted-line {
        border-color: $blue;
        color: $blue;
      }
    }

    &__today {
      &::before {
        content: '';
        position: absolute;
        bottom: 0;
        top: 5%;
        border-left: 1px dotted #000;
        transform: translateX(-50);
      }
    }
  }

  &-time-graph {
    &__time {
      @extend .font-size-12;
      color: $gray-6;
      position: relative;
      margin-top: 0.5rem;
      display: flex;

      span {
        &:nth-of-type(1) {
          width: 15%;
          text-align: left;
        }

        &:nth-of-type(2) {
          width: 27%;
          text-align: center;
        }

        &:nth-of-type(3) {
          width: 15%;
          text-align: center;
        }

        &:nth-of-type(4) {
          width: 20%;
          text-align: right;
        }

        &:nth-of-type(5) {
          width: 12%;
          text-align: right;
        }
      }
    }

    &__bar {
      position: relative;
      border-top: 1px dotted #e6e6e8;
      border-bottom: 1px solid $border-color-d;
      padding: 0.4rem 0;
      height: 4.8rem;
      display: flex;

      &::before {
        content: '';
        border-top: 1px dotted #e6e6e8;
        height: 0;
        width: 100%;
        position: absolute;
        left: 0;
        top: 50%;
      }

      &::after {
        content: '';
        display: block;
        border-left: 1px dotted $black;
        height: 100%;
        width: 0;
        position: absolute;
        right: calc(3.64% - 0.5px);
        top: 0;
        bottom: 0;
        z-index: 2;
      }


      span {
        position: relative;
        z-index: 2;
        display: flex;
        height: 100%;
        font-size: 0;
        background: $brand-y;
      }
    }
  }

  &-progress {
    ul {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 100%;
      height: 100%;
      position: relative;

      &::before {
        content: '';
        position: absolute;
        left: 5.3rem;
        top: -0.5rem;
        bottom: -0.5rem;
        width: 1px;
        background: $border-color-d;
      }
    }

    li {
      display: flex;
      width: 100%;
      justify-content: flex-start;
      align-items: center;

      & + li {
        margin-top: 3.425rem;
      }
    }

    label {
      font-size: 1.2rem;
      line-height: 0;
      width: 6.725rem;
      position: relative;
      top: -0.1rem;
      color: $gray-6;
      text-align: right;
      padding-right: 1.1rem;
      white-space: nowrap;

      strong {
        color: $black;
        font-weight: bold;
      }
    }

    &__bar {
      @include progress(#eaeaea);

      height: 0.8rem;
      overflow: inherit;

