        position: relative;
      }

      em {
        margin-top: 0.5rem;
        position: absolute;
        top: 100%;
        right: 0;
        font-size: 1.2rem;
        font-weight: bold;
        line-height: 1.48;
        white-space: nowrap;
      }
    }
  }

  &-average-bar {
    position: relative;

    &::before {
      content: '';
      display: block;
      width: 100%;
      height: 0.4rem;
      background: $border-color-d;
      position: absolute;
      bottom: 0.95rem;
    }

    ul {
      display: flex;
      align-content: center;
      justify-content: space-between;

      &::before,
      &::after {
        content: '';
        display: block;
        border-radius: 100%;
        width: 1.1rem;
        height: 1.1rem;
        background: $gray-9;
        position: absolute;
        bottom: 0.6rem;
      }

      &::before {
        left: 0;
      }

      &::after {
        right: 0;
      }
    }
    li {
      position: relative;
      height: 5.2rem;
      padding-bottom: 3.1rem;
      align-items: center;

      em {
        font-size: 1.2rem;
        font-weight: 700;
        color: rgba($gray-6, 0.8);
      }

      strong {
        font-size: 1.6rem;
        font-weight: 700;
        color: rgba($black, 0.8);
      }

      &.active {
        &::before,
        &::after {
          content: '';
          display: block;
          border-radius: 100%;
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
        }

        &::before {
          bottom: 0;
          width: 2.3rem;
          height: 2.3rem;
          opacity: 0.8;
          box-shadow: 0 0.3rem 1.2rem 0 rgba(0, 0, 0, 0.3);
          border: 1px solid  $brand-y;
          background-color: #ffffff;
        }

        &::after {
          width: 1.1rem;
          height: 1.1rem;
          bottom: 0.7rem;
          background: $brand-y;
        }
      }

      &:last-child.active {
        &::before,
        &::after {
          left: inherit;
          right: 0;
          transform: none;
        }

        &::before {
          bottom: 0;
          z-index: 2;
          transform: translateX(25%);
        }

        &::after {
          z-index: 3;
        }
      }
    }
  }

