.modal-address-select {
  &__body {
    padding: 0.5rem 0;
  }

  &__title {
    @extend .font-size-16;
    text-align: left;
    margin-bottom: 1.5rem;
  }

  &__lists {
    li + li {
      margin-top: 1.2rem;
    }
  }

  &__button {
    display: flex;
    align-items: center;
    width: 100%;
    height: 5.4rem;
    border: 1px solid $border-color-d;
    border-radius: 1rem;
    padding: 0 1.5rem;

    span {
      @extend .font-size-16;
      flex: 1 0 0;
      margin-left: 0.75rem;
      text-align: left;
      display: flex;
      justify-content: space-between;
      position: relative;

      &::before,
      &::after {
        content: '';
        position: absolute;
        display: inline-block;
        top: 50%;
        transform: translateY(-50%);
        border-radius: 50%;
        box-sizing: border-box;
        background-color: $white;
      }

      &::before {
        right: 0;
        width: 2.7rem;
        height: 2.7rem;
        border: 1px solid $border-color-a;
        background-color: $white;
      }

      &::after {
        z-index: 1;
        right: 0.8rem;
        width: 1.1rem;
        height: 1.1rem;
      }
    }

    &.active span {
      &::before {
        border: 1px solid $secondary-green;
        background-color: $secondary-green;
      }
    }

    &:disabled {
      i {opacity: 0.5;}

      span {
        &::before {
          border: 1px solid $border-color-d;
          background-color: $border-color-e;
        }

        &::after {
          display: none;
        }
      }
    }
  }
}

.survey {
  &__wrap {
    display: flex;
    flex-direction: column;
  }
  &__content {
    padding: 2rem 1.6rem;
    flex-grow: 1;
    overflow-y: auto;
    overflow-x: hidden;
  }
  &__progress {
    position: relative;
    flex-shrink: 0;
    height: 0.5rem;
    background-color: $bg-color-ea;
    transition: all 0.3s;
    &--active {
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      background-color: $secondary-green;
      transition: all 0.3s;
    }
  }
}

.survey-height {
  &__title {
    padding: 1.4rem 1.5rem 0;
    font-size: 1.8rem;
    font-weight: 500;
    line-height: 1.67;
    color: $black;
    @extend .kb-font;
    strong {
      font-weight: 700;
      vertical-align: baseline;
    }
  }
  &__swiper {
    display: flex;
    align-items: center;
    justify-content: space-around;
    &-item {
      &-title{
        font-size: 1.6rem;
        font-weight: bold;
        line-height: 1.38;
        letter-spacing: normal;
        text-align: center;
        color: $black;
        @extend .kb-font;
      }
      &-slide {
        position: relative;
        .calender-swiper {
          position: relative;
          &::before,
          &::after {
            content: '';
            position: absolute;
            left: 0;
            width: 100%;
            height: 5.2rem;
            object-fit: contain;
            z-index: 2;
          }
          &::before {
            top: 0;
            background-image: linear-gradient(to bottom, #fff, rgba(255, 255, 255, 0));
          }
          &::after {
            bottom: 0;
            background-image: linear-gradient(to top, #fff, rgba(255, 255, 255, 0));
          }
          margin: 0;
          // .swiper-slide-active {
          //   height: 5.5rem !important;
          // }
          .swiper-container {
            height: 20rem;
          }
          .swiper-container::before {
            top: 7.3rem;
          }
          .swiper-container::after {
            bottom: 7.3rem;
          }
        }
      }
      &-tag {
        position: absolute;
        right: -2.5rem;
        top: calc(50% - 0.8rem);
        font-size: 1.6rem;
        color: $gray-6;
      }
    }
  }
  &__box {
    background-color: $bg-color-f;
    border-radius: 1rem;
    padding: 1.6rem;
    font-size: 1.8rem;
    text-align: center;
    @extend .kb-font;
    color: $black;
    strong {
      font-weight: 700;
      vertical-align: baseline;
    }
    .custom-input {
      display: inline-block;
      width: 7.5rem;
      margin: 0 0.5rem;
      input[type="text"]:disabled {
        border-color: $border-color-d;
        background: $white;
        color: $black;
        @extend .noto-font;
      }
    }
  }
}

