@charset "utf-8";
// 

.board {
  word-break: keep-all;

  &__top {
    padding-bottom: 2rem;
    border-bottom: 1px solid $border-color-d;
  }

  &__title {
    color: $black;
    @extend .font-size-16;
    line-height: 1.66;
  }

  &__sub-text {
    margin-top: 1rem;
    color: $gray-9;
    @extend .font-size-14;
    line-height: 1.61;

    &-and {
      margin-top: 0.5rem;
      line-height: 2.25rem;
      color: $gray-9;
      @extend .font-size-14;

      &.on {
        color: $brand-y;
      }
    }
  }

  &__content {
    padding: 2rem 0;
    @extend .font-size-16;
    color: $gray-6;
    line-height: 1.66;
    word-break: keep-all;

    &.bg {
      background: $bg-color-f;
    }
  }
}

// faq
.faq {
  &__list {
    margin-top: 2.5rem;
  }
}

// 1:1  float-icon
.float-icon {
  position: fixed;
  right: 1.6rem;
  bottom: 7.6rem;
  right: calc(constant(safe-area-inset-right) + 1.6rem);
  right: calc(env(safe-area-inset-right) + 1.6rem);
  bottom: calc(constant(safe-area-inset-bottom) + 7.6rem);
  bottom: calc(env(safe-area-inset-bottom) + 7.6rem);
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  overflow: hidden;
  background: $brand-y;
  z-index: 10;

  button {
    display: block;
    width: 100%;
    height: 100%;
    padding-bottom: 0.4rem;
    color: $white;

    span {
      display: block;
      margin-top: 0.2rem;
      @extend .font-size-14;
    }
  }

  &.small {
    width: 5rem;
    height: 5rem;
    box-shadow: 0 0 0.5rem 0 rgba($black, 0.35);

    button {
      padding-bottom: 0.2rem;

      span {
        @extend .font-size-12;
        line-height: 1.04;
      }
    }
  }
}

// 1:1  list
.inquiry {
  &__list {
    .card-item {
      margin-bottom: 1rem;

      &__date {
        display: flex;
        justify-content: flex-start;
        align-items: center;

        > * {
          position: relative;
          padding: 0 1rem;
          @extend .font-size-14;

          + * {
            &::before {
              position: absolute;
              top: calc(50% - 0.6rem);
              left: 0;
              width: 1px;
              height: 1.4rem;
              background: $border-color-d;
              content: '';
            }
          }
        }

        time {
          margin-left: -1rem;
        }

        em {
          color: $secondary-green;

          &.end {
            color: $black;
          }
        }
      }
    }
  }

  &__question {
    margin-bottom: 2rem;

    &-title {
      @extend .font-size-16;
      position: relative;
      padding-left: 2.6rem;
      margin-bottom: 1.5rem;
      color: $black;
      word-break: keep-all;
      line-height: 2.2rem;

      &::before {
        position: absolute;
        top: 0.3rem;
        left: 0;
        @extend .icon-qna_q;
        @extend .icon-16;
        content: '';
      }
    }

    &-date {
      margin-bottom: 1rem;
      color: $gray-9;
      @extend .font-size-14;
    }

    &-content {
      margin-bottom: 1rem;
      color: $gray-6;
      @extend .font-size-16;
      word-break: keep-all;
      line-height: 1.66;
    }
  }

  &__answer {
    padding: 1.5rem;
    background: $bg-color-f;

    &-title {
      position: relative;
      padding-left: 2.6rem;
      margin-bottom: 1.5rem;
      color: $black;
      @extend .font-size-16;
      line-height: 2.2rem;
      word-break: keep-all;

      &::before {
        position: absolute;
        top: 0.3rem;
        left: 0;
        @extend .icon-qna_a;
        @extend .icon-16;
        content: '';
      }
    }

    &-date {
      margin-bottom: 1rem;
      color: $gray-9;
      @extend .font-size-14;
    }

    &-content {
      @extend .font-size-16;
      word-break: keep-all;
      line-height: 1.6;
      color: $black;
    }
  }

  &__form {
    &-section {
      margin-bottom: 1.5rem;

      &-button {
        margin-top: 3rem;
      }
    }
  }

  &__file {
    &-add {
      margin-bottom: 1.2rem;
      text-align: right;
    }

    &-name {
      position: relative;
      padding: 1.62rem 1.5rem;
      margin-bottom: 1rem;
      border-radius: $input-radius;
      background: $bg-color-f;

      span {
        @extend .font-size-16;
        line-height: 1.38;
      }

      button {
        position: absolute;
        right: 0.8rem;
        top: 1.25rem;
      }
    }

    &-info {
      color: $gray-9;
      @extend .font-size-14;
    }
  }
}