@charset "utf-8";

// input
.input {
  &-radio {
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    @extend .font-size-16;

    i {
      position: relative;
      font-size: 0;

      &::before {
        display: inline-block;
        content: '';
        width: 2.7rem;
        height: 2.7rem;
        margin-right: 1rem;
        border-radius: 50%;
        border: 1px solid $border-color-a;
        background-color: $white;
        box-sizing: border-box;
      }

      &::after {
        position: absolute;
        z-index: 1;
        left: 0.8rem;
        top: 0.8rem;
        width: 1.1rem;
        height: 1.1rem;
        border-radius: 50%;
        background-color: $white;
        content: '';
      }
    }

    input {
      width: 0;
      height: 0;
      opacity: 0;
      position: absolute;

      &:checked {
        + i {
          &::before {
            border: 1px solid $secondary-green;
            background-color: $secondary-green;
          }
        }
      }

      &:disabled {
        + i {
          &::before {
            border: 1px solid $border-color-d;
            background-color: $border-color-e;
          }

          &::after {
            display: none;
          }
        }

        &:checked {
          + i {
            &::before {
              border: 1px solid $border-color-e;
            }

            &::after {
              display: block;
            }
          }
        }
      }
    }
  }

  &-checkbox {
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    @extend .font-size-16;

    strong {
      font-weight: 600;
      vertical-align: top;
    }

    i {
      position: relative;
      font-size: 0;

      &::before {
        display: inline-block;
        content: '';
        width: 2.7rem;
        height: 2.7rem;
        margin-right: 1rem;
        border-radius: 5px;
        border: 1px solid $border-color-a;
        background-color: $white;
        box-sizing: border-box;
      }

      &::after {
        position: absolute;
        z-index: 1;
        left: 0.55rem;
        top: 0.55rem;
        @include svgIconColor(check, 'fff');
        @extend .icon-16;
        content: '';
      }
    }

    input {
      position: absolute;
      width: 0;
      height: 0;
      opacity: 0;

      &:checked {
        + i {
          &::before {
            border: 1px solid $secondary-green;
            background-color: $secondary-green;
          }
        }
      }

      &:disabled {
        + i {
          &::before {
            border: 1px solid $border-color-d;
            background-color: $border-color-e;
          }

          &::after {
            display: none;
          }
        }

        &:checked {
          + i {
            &::before {
              border: 1px solid $border-color-e;
            }

            &::after {
              display: block;
            }
          }
        }
      }
    }

    &.only-check {
      i {
        &::before {
          border: none;
        }

        &::after {
          @include svgIconColor(check, 'e9e9e9');
        }
      }
      span {
        color: $gray-b;
      }

      input {
        width: 0;
        height: 0;
        opacity: 0;
        &:checked {
          + i {
            &::before {
              border: none;
              background: none;
            }

            &::after {
              @include svgIconColor(check, '76b424');
            }
            + span {
              color: $black;
            }
          }
        }

        &:disabled {
          + i {
            &::before {
              border: none;
              background: none;
            }

            &::after {
              display: block;
              @include svgIconColor(check, 'f6f7fa');
            }
          }

          &:checked {
            + i {
              &::before {
                border: none;
              }

              &::before {
                background-color: none;
              }

              &::after {
                @include svgIconColor(check, 'f6f7fa');
              }
            }
          }
        }
      }
    }

    &.box {
      min-width: 15.8rem;

      span {
        display: block;
        width: 100%;
        padding: 1.02rem 4.8rem 1.02rem 1.5rem;
        border: 1px solid $border-color-a;
        color: $gray-9;
        position: relative;

        &::after {
          position: absolute;
          content: '';
          top: 0.9rem;
          right: 1.5rem;
          @include svgIconColor(check, '999');
          @extend .icon-18;
        }
      }

      i {
        display: none;
      }

      input {
        &:checked {
          + span {
            border: 1px solid $secondary-green;
            color: $secondary-green;
            font-weight: 700;

            &::after {
              @include svgIconColor(check, '76b424');
            }
          }
        }

        &:disabled {
          + span {
            border: 1px solid $border-color-e;
            color: $border-color-e;

            &::after {
              @include svgIconColor(check, 'e6e6e8');
            }
          }
        }
      }
    }
  }

  &-switch {
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 4.8rem;
    height: 2.7rem;

    i {
      position: relative;
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 2.0rem;
      background: $border-color-a;
      transition: $base-duration;

      &::before {
        position: absolute;
        left: 0.3rem;
        top: 0.3rem;
        width: 2.1rem;
        height: 2.1rem;
        border-radius: 50%;
        background-color: $white;
        content: '';
        transition: $base-duration;
      }
    }

    input {
      width: 0;
      height: 0;
      opacity: 0;
      &:checked {
        + i {
          background-color: $secondary-green;

          &::before {
            transform: translateX(2.1rem);
          }
        }
      }

      &:disabled {
        + i {
          background-color: $border-color-e;
        }
      }
    }
  }

  &-switch-text {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 3.3rem;
    border-radius: 1.85rem;
    border: 1px solid $border-color-a;
    padding: 0.3rem;
    overflow: hidden;
    min-width: 9.3rem;

    span {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      white-space: nowrap;

      &::before {
        content: '';
        position: absolute;
        left: 0;
        width: 50%;
        height: 100%;
        background: $secondary-green;
        border-radius: 2.7rem;
        transition: all $base-duration;
      }

      em {
        position: relative;
        display: block;
        flex: 1 0 0;
        font-size: 1.4rem;
        padding: 0 0.838rem;
        z-index: 2;
        transition: all $base-duration;

        &:nth-of-type(1) {
          color: $white;
        }

        &:nth-of-type(2) {
          color: $gray-6;
        }
      }
    }

    input {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;

      &:checked {
        & + span {
          &::before {
            left: 50%;
          }

          em {
            &:nth-of-type(1) {
              color: $gray-6;
            }

            &:nth-of-type(2) {
              color: $white;
            }
          }
        }
      }
    }
  }
}

// input : text
@mixin inputTextStyle () {
  text-align: left;
  width: 100%;
  border: 1px solid $border-color-a;
  border-radius: $input-radius;
  @extend .font-size-16;

  &:focus {
    border-color: $border-color-7;
  }

  &:disabled {
    border-color: $border-color-d;
    background: $gray-e;
    color: $gray-b;
    @media (prefers-color-scheme: dark) {
      color: $gray-b;
    }
  }

  .error & {
    border-color: $border-color-red;
  }


  &.number {
    text-align: right;
  }
  &.pr70 {
    padding-right: 7.0rem;
  }
  &.pr85 {
    padding-right: 8.5rem;
  }
  &.pr100 {
    padding-right: 10rem;
  }

  &::placeholder {
    color: $gray-9;
  }
}

input {
  &[type="text"],
  &[type="password"],
  &[type="number"],
  &[type="tel"],
  &[type="url"],
  &[type="email"] {
    height: $input-height;
    padding: 0 $input-padding;
    @include inputTextStyle;
  }
}

textarea {
  padding: $input-padding;
  line-height: 1.65625;
  @include inputTextStyle;
}

.custom-select {
  position: relative;
  display: block;
  width: 100%;
  line-height: 0;
  background: $white;

  * {
    transition: all 0.3s;
  }

  dl {
    position: relative;

   &.default {
      button span {
        color: $gray-9;
      }
    }
  }

  dt {
    border: 1px solid $border-color-a;
    border-radius: $input-radius;
    height: $input-height;
    button {
      position: relative;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 $input-padding;
      text-align: left;

      span {
        @include ellipsis;
        @extend .font-size-16;

        display: block;
        width: 90%;
        align-items: center;
        white-space: nowrap;
        color: $black;
      }

      &::after {
        content: '';
        position: relative;
        top: 1px;
        transition: all $base-duration;

        @extend .icon-select;
        @extend .icon-18;
      }

      &:disabled {
        &::after {
          opacity: 0.4;
        }
        span {
          color: $gray-9;
        }
      }
    }
  }
  dd {
    position: absolute;
    top: 100%;
    margin-top: -1px;
    max-height: 0;
    z-index: 10;
    width: 100%;
    overflow: hidden;
    border: 1px solid transparent;
  }
  li {
    overflow-x: hidden;
    overflow-y: auto;

    button {
      font-size: 1.6rem;
    }
  }

  &.active {
    dt {
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;

      button {
        &::after {
          transform: rotate(180deg);
        }
      }
    }

    dd {
      max-height: 12.05rem;
      overflow-y: scroll;
      -webkit-overflow-scrolling: touch;
      padding: 1rem 0;
      border-color: $border-color-a;
      background: $white;
      border-bottom-left-radius: $input-radius;
      border-bottom-right-radius: $input-radius;
    }

    ul {
      display: block;
    }

    li {
      button {
        width: 100%;
        height: 3.35rem;
        text-align: left;
        padding: 0 1.4rem;
        color: $gray-6;
      }

      &.selected {
        background: $bg-color-f;
      }
    }
  }
}

.custom-input {
  $root: &;

  position: relative;

  &.error {
    &::after {
      content: '';
      position: absolute;
      right: 1.5rem;
      top: 1.4rem;

      @extend .icon-error;
      @extend .icon-18;
    }
  }

  &.width50 {
    width: calc(50% - 1rem);
  }

  &__suffix {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    z-index: 2;
    display: flex;
    align-items: center;
    min-width: 4.4rem;
    min-height: 4.4rem;

    .only-icon {
      padding-top: 0.1rem;
    }

    :disabled + & {
      .only-icon {
        opacity: 0.3;
      }

      #{$root}__unit {
        color: $gray-b;
      }
    }
  }

  &.with-prefix {
    input {
      padding-left: 4rem;
    }
  }
  &__prefix {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 4.8rem;
    min-height: 4.8rem;

    .only-icon {
      padding-top: 0.1rem;
    }

    :disabled + & {
      .only-icon {
        opacity: 0.3;
      }

      #{$root}__unit {
        color: $gray-b;
      }
    }
  }

  &__unit {
    font-size: 1.6rem;
    line-height: 1.25;
    text-align: right;
    color: $gray-6;
    padding: 0 1.5rem 0 0.5rem;
  }
}

.form-item {
  @extend .font-size-16;

  position: relative;
  text-align: left;

  &__text {
    font-size: 1.4rem;
    line-height: 1.43;
    color: $gray-9;
    margin-top: 1rem;
  }

  &__guide {
    margin-top: 1rem;
    font-size: 1.6rem;
    line-height: 1.25;
    text-align: right;
    color: $gray-6;
  }

  &__error {
    @extend .font-size-14;

    position: relative;
    display: none;
    text-align: right;
    margin-top: 1.0rem;
    line-height: 1.375;
    color: $border-color-red;

    .error-fixed & {
      position: absolute;
      right: 0;
    }

    .error + & {
      display: block;
    }
  }

  &__flex {
    display: flex;
    width: 100%;
    align-items: center;

    > * + * {
      margin-left: 1rem;
    }

    .narrow-space {
      margin: 0 0.7rem;
      color: $gray-9;

      & + * {
        margin-left: 0;
      }
    }
  }
}

/* table form */
.table-form {
  $root: &;

  &__item {
    &-label {
      @extend .font-size-14;

      display: flex;
      width: 100%;
      align-items: baseline;
      justify-content: space-between;
      line-height: 1.4285;
      color: $gray-6;
      margin-bottom: 1.5rem;

      > i {
        font-weight: 300;
        font-style: normal;
        &.black {
          color: $black;
        }
      }

      &.es {
        label {
          &::after {
            content: '*';
            color: $brand-y;
            margin-left: 0.1rem;
          }
        }
      }

      &.bold {
        margin-bottom: 1rem;

        label {
          @extend .kb-font;
          font-size: 1.6rem;
          color: $black;
        }

        > i {
          color: $black;
        }
      }

      .sub-text {
        font-size: 1.6rem;
        font-weight: 400;
        color: $gray-9;
      }
    }

    .tooltip-button {
      display: inherit;
    }

    .tooltip-button .only-icon {
      margin-left: 0.6rem;
      position: relative;
      top: -0.1rem;
      min-width: inherit;
      min-height: inherit;
    }

    & + & {
      padding-top: 2.5rem;
    }
  }

  &.lined {
    #{$root}__item {
      padding-bottom: 2.2rem;

      & + #{$root}__item {
        border-top: 1px solid $border-color-d;
      }
    }
  }
}

.table-data {
  dl {
    display: flex;
    line-height: 1.6;

    & + dl {
      margin-top: 2.5rem;
    }
  }

  dt {
    @extend .font-size-14;
    width: 7.65rem;
    flex: 0 0 auto;
    padding: 0.13rem 0;
  }

  dd {
    @extend .font-size-16;
    flex: 1 0 0;
    position: relative;

    .only-icon {
      position: relative;
      margin: -0.3rem 0 0 -0.5rem;
    }
  }

  &.lined {
    dl {
      border: solid $border-color-d;
      border-width: 1px 0;
      margin-top: -1px;
    }

    dt {
      padding: 1.63rem 0;
    }
    dd {
      padding: 1.5rem 0;
    }
  }

  &__gray-box {
    @extend .font-size-16;

    background: $bg-color-f;
    border-radius: 0.8rem;
    line-height: 1.65625;
    padding: 1.5rem;

    p + p {
      margin-top: 1.5rem;
    }
  }

  &__showing {
    @extend .font-size-14;

    display: block;
    color: $gray-9;
    line-height: 1.6071;
    padding-left: 1.5rem;
    position: relative;
    word-break: keep-all;
    margin-top: 1rem;

    &::before {
      content: '*';
      position: absolute;
      left: 0;
      top: 0;
    }
  }

  &__button-ab {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
  }
}

/*  */
.accordion {
  > dl {
    > dt {
      display: block;
  
      button {
        display: flex;
        justify-content: space-between;
        align-items: center;
        text-align: left;
        background: transparent;
      }
  
      i {
        @extend .icon-circle_down;
        @extend .icon-20;
        transition: all $base-duration;
      }
    }
  
    > dd {
      display: none;
    }  
  } 
  > dl.active {
    > dt {
      i {
        @extend .icon-circle_up;
        @extend .icon-20;
      }
    }
  }

  &--case1,
  &--case2 {
    > dl {
      border-style: solid;
      border-color: $border-color-a;
      line-height: 1.65625;
      overflow: hidden;
      > dt {
        transition: all $base-duration;
  
        button {
          width: 100%;
          height: 5.35rem;
          padding: 0 $input-padding;
        }
  
        span {
          @extend .font-size-16;
          @include ellipsis;
          display: block;
          width: calc(100% - 3rem);
          text-align: left;
        }
      }
      > dd {
        padding: $input-padding;
      }
    }



    .active {
      > dt {
        background: $bg-color-f;
      }
    }
  }

  &--case1 {
    > dl {
      border-width: 1px;
      border-radius: $input-radius;
    }
  }

  &--case2 {
    > dl {
      border-width: 1px 0;

      & + dl {
        margin-top: -1px;
      }
    }
  }

  &--case3 {
    > dl {
      > dt {
        display: flex;
        align-items: center;
        margin-bottom: 1rem;
        line-height: 1.25;

        span {
          @extend .font-size-16;
          margin-right: 1rem;
          white-space: nowrap;
        }

        em {
          @extend .font-size-16;
          font-weight: 600;
          border-bottom: 1px solid $black;
        }
      }
    }
  }

  &__text-list {
    @extend .font-size-16;

    p {
      padding: 1rem 0;

      & + p {
        border-top: 1px solid $border-color-d;
      }
    }
  }

  &__button-list {
    button {
      @extend .font-size-14;
      width: 100%;
      text-align: left;

      & + button {
        margin-top: 1.5rem;
      }
    }
  }

  &__box-list {
    padding: 2rem;
    border: 1px solid $border-color-d;

    &-item {

      & + & {
        margin-top: 2rem;
      }
    }

    &-text {
      @extend .font-size-14;
      line-height: 1.4285;
    }

    &-info {
      display: block;
      margin-top: 1rem;
      color: $gray-9;

      span + span {
        position: relative;
        padding-left: 1rem;
        margin-lefT: 0.5rem;

        &::before {
          content: '';
          display: inline-block;
          width: 1px;
          height: 1.4rem;
          background: $gray-9;
          position: absolute;
          left: 0;
          top: 50%;
          transform: translateY(-45%);
        }
      }
    }
  }
}

