@charset "utf-8";

.tag {
  @extend .font-size-11;
  display: inline-block;
  padding: 0.4rem 0.6rem 0.4rem 0.5rem;
  background-color: $secondary-green;
  border-radius: 15rem;
  color: $white;
  line-height: 1;
  font-style: normal;

  &--care-step1 { background: #4da2fb !important;}
  &--care-step2 { background: #52bcc9 !important;}
  &--care-step3 { background: #f57620 !important;}
  &--care-step4 { background: #dc4649 !important;}

  &--sub {
    background: $brand-y;
  }

  &--fixed {
    width: 5.05rem;
    text-align: center;
  }

  &-pick {
    @extend .noto-font;
    display: inline-block;
    text-align: center;
    font-size: 1.1rem;
    line-height: 1.48;
    border-radius: 1rem;
    padding: 0.1rem 0.5rem 0.2rem;
    min-width: 6rem;
    white-space: nowrap;
    color: $white;
    font-style: normal;

    &--main {
      background: $brand-y;
    }

    &--sub {
      background: $secondary-green;
    }
  }
}

.tab {
  $tab: &;
  position: relative;
  overflow-x: auto;
  overflow-y: hidden;
  text-align: center;
  font-size: 0;
  white-space: nowrap;
  border-bottom:1px solid $border-color-e;
  // &::before {
  //   position: absolute;
  //   bottom: 0;
  //   left: 0;
  //   content: '';
  //   width: 100%;
  //   height: 1px;
  //   z-index: 1;
  //   background: $border-color-e;
  // }
  &.with-depth {
    margin-bottom: 1.5rem;
  }
  &.with-aco {
    padding-right: 3rem;
  }
  &__wrap {
    position: relative;
  }
  &__more {
    position: absolute;
    top: 0.3rem;
    right: 0;
    padding: 0 0.6rem;
    button {
      position: relative;
      z-index: 2;
    }
    i {
      font-size: 0;
      @extend .icon-20;
      @include svgIconColor(accordion, '76b424');
    }
    z-index: 2;
    &::after {
      content: '';
      position: absolute;
      width: 5rem;
      height: 100%;
      right: 0;
      top: 0;
      background-image: linear-gradient(to left, #ffffff, rgba(255, 255, 255, 0.9) 63%, rgba(255, 255, 255, 0));
      z-index: 1;
    }
    &-and {
      position: absolute;
      top: 3.9rem;
      display: none;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap;
      width: 100%;
      padding: 2.25rem 1.6rem;
      background-color: $white;
      z-index: $header-z-index + 3;
      &.on {
        display: flex;
      }
      li {
        button {
          width: 100%;
          height: 100%;
          color: $gray-9;
        }
        width: calc(50% - 0.75rem);
        margin: 0.75rem 0;
        height: 4.4rem;
        border: 1px solid $border-color-a;
        border-radius: 1rem;
        text-align: center;
        @extend .font-size-16;
        color: $gray-9;
        &.on {
          border: none;
          background-color: $secondary-green;
          color: $white;
          button {
            color: $white;
          }
        }
      }
    }
    &-bg {
      display: none;
      position: absolute;
      top: 10.3rem;
      width: 100%;
      height: 100%;
      background: rgba($black, 0.7);
      z-index: $header-z-index + 2;
      &.on {
        display: block;
      }
    }
  }
  &__item {
    display: inline-block;
    position: relative;
    margin: 0 2rem;
    cursor: pointer;
    // &::before {
    //   position: absolute;
    //   bottom: 0;
    //   left: -2rem;
    //   content: '';
    //   width: calc(100% + 4rem);
    //   height: 1px;
    //   z-index: 1;
    //   background: $border-color-e;
    // }
    span {
      display: inline-block;
      padding: 1.2rem 0 1rem;
      white-space: nowrap;
      line-height: 1;
      @extend .kb-font;
      @extend .font-size-16;
      color: $gray-6;
    }
    &--on {
      span {
        color: $black;
        font-weight: 700;
        &::after {
          content: '';
          position: absolute;
          left: 0;
          bottom: 0;
          z-index: 2;
          width: 100%;
          height: 0.3rem;
          border-radius: 0.3rem;
          background-color: $black;
        }
      }
    }
  }
  &__content {
    display: none;
    //     
    &.bg {
      margin: 0 2rem;
      padding: 3rem;
      background-color: $gray-b;
      text-align: center;
      color: $white;
    }
    &--on {
      display: block;
    }
  }
  &.depth02 {
    text-align: left;
    width: auto;
    border: none;
    &::before {
      display: none;
    }
    #{$tab}__item {
      &:nth-of-type(1)::before {
        display: none;
      }
      margin: 0 0.6rem;
      span {
        display: inline-block;
        padding: 0.52rem 1rem 0.4rem;
        line-height: 2.2rem;
        @extend .font-size-14;
        color: $gray-9;
        font-family: 'Noto Sans KR';
        &::after {
          display: none;
        }
      }
      + #{$tab}__item {
        position: relative;
        &::before {
          position: absolute;
          left: -0.6rem;
          top: calc(50% - 1.15rem);
          content: '';
          width: 1px;
          height: 2.3rem;
          background: $border-color-d;
        }
      }
      &--on {
        span {
          background-color: $secondary-green;
          border-radius: 15rem;
          color: $white;
          font-weight: 400;
        }
      }
    }
  }
  &.button-type {
    border: none;
    overflow-x: hidden;
    text-align: left;
    #{$tab}__item {
      button {
        width: 5.6rem;
        height: 5.6rem;
        border: 1px solid $border-color-a;
        border-radius: 1.1rem;
      }
      margin: 0 0.5rem;
      span {
        @extend .noto-font;
        padding: 0;
        color: $gray-9;
      }
      &--on {
        button {
          border: 1px solid $secondary-green;
          background: $secondary-green;
        }
        span {
          color: $white;
          font-weight: 700;
          &::after {
            display: none;
          }
        }
      }
    }
  }
}
