html, body { background: #fff; position: relative; touch-action: none; -ms-touch-action: none; }

html { font-family: 'Noto Sans KR'; }

html img { max-width: 100%; }

html.outer-overflow { overflow: hidden; }

html.outer-overflow body, html.outer-overflow .wrapper { overflow: hidden; }

.wrapper { position: relative; padding-top: env(safe-area-inset-top); padding-bottom: env(safe-area-inset-bottom); padding-right: env(safe-area-inset-right); padding-left: env(safe-area-inset-left); padding-top: constant(safe-area-inset-top); padding-bottom: constant(safe-area-inset-bottom); padding-right: constant(safe-area-inset-right); padding-left: constant(safe-area-inset-left); }

.wrapper.footer-hide .bottom-fixed, .wrapper.footer-hide .footer { bottom: -10rem; }

.wrapper.footer-hide .footer__button-nav { bottom: -15rem; }

.wrapper.footer-hide .content-wrap { padding-bottom: 0; }

.header, .footer { position: absolute; left: 0; width: 100%; z-index: 200; transition: all 0.3s; }

.header { top: 0; top: constant(safe-area-inset-top); top: env(safe-area-inset-top); z-index: 205; padding-right: env(safe-area-inset-right); padding-left: env(safe-area-inset-left); padding-right: constant(safe-area-inset-right); padding-left: constant(safe-area-inset-left); }

.footer { bottom: 0; padding-bottom: constant(safe-area-inset-bottom); padding-bottom: env(safe-area-inset-bottom); padding-right: env(safe-area-inset-right); padding-left: env(safe-area-inset-left); padding-right: constant(safe-area-inset-right); padding-left: constant(safe-area-inset-left); }

.footer__nav { background: #f6f7fa; border-top: 1px solid #e6e6e8; }

