@charset "utf-8";

.my-page {
  padding: 0 !important;
  &__wrap {
    padding: 1.6rem 1.6rem 3rem;
  }
  &__list {
    i {
      margin-right: 0.6rem;
      margin-bottom: 0.3rem;
      @extend .icon-16;
    }
  }
  &__summary {
    position: relative;
    margin: 2rem 0 1rem;
    background: $brand-g;
    &::before {
      position: absolute;
      width: 100%;
      height: 6rem;
      left: 0;
      top: 0;
      background: $white;
      content: '';
      z-index: 1;
    }
  }
  &__profile {
    position: relative;
    width:100%;
    z-index: 2;
    color: $white;
    &-img {
      width: 11.6rem;
      height: 11.6rem;
      margin: 0 auto 1rem;
      border-radius: 50%;
      border: 0.8rem solid $white;
      overflow: hidden;
      background-color: $bg-color-f;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
      &-no {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        i {
          width: 5rem;
          height: 5rem;
        }
      }
    }
    &-text {
      text-align: center;
      @extend .font-size-18;
      strong {
        vertical-align: baseline;
        font-weight: 700;
        @extend .kb-font;
      }
    }
    &-info {
      position: absolute;
      right: 1.6rem;
      top: 7rem;
      @extend .font-size-14;
      i {
        margin-right: 0.4rem;
        margin-bottom: 0.3rem;
      }
    }
  }
  &__counts {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2rem 0;
    color: $white;
    li {
      position: relative;
      text-align: center;
      width: 10.8rem;
      button {
        color: $white;
        width: 100%;
      }
      p {
        margin-bottom: 0.5rem;
        @extend .font-size-14;
        i {
          margin-bottom: 0.3rem;
          margin-right: 0.4rem;
        }
      }
      b {
        @extend .font-size-18;
        @extend .kb-font;
      }
      + li {
        &::before {
          position: absolute;
          top: calc(50% - 1.5rem);
          left: 0;
          content: '';
          width: 1px;
          height: 3.5rem;
          background: rgba($white, 0.5);
        }
      }
    }
  }
  &__point-box {
    margin: 2rem 1.6rem;
    padding: 1.5rem;
    color: $white;
    background: $bg-color-77;
    border-radius: $input-radius;
  }
  &__point {
    &-care {
      text-align: center;
      &-title {
        margin: 0.5rem 0;
        @extend .kb-font;
        @extend .font-size-18;
        line-height: 1.67;
        strong {
          display: inline-block;
          margin-left: 0.3rem;
          vertical-align: baseline;
          font-weight: 700;
        }
      }
      &-sub {
        margin-bottom: 0.5rem;
        @extend .font-size-14;
        i {
          margin-left: 0.4rem;
          vertical-align: bottom;
          @include iconFilter;
        }
      }
    }
  }
}

// 
.my-care {
  height: calc(100% - 12.2rem);
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @extend .font-size-16;
    &.sub {
      margin-top: 1rem;
      color: $gray-9;
      @extend .font-size-14;
    }
    li {
      word-break: keep-all;
      &:nth-of-type(2) {
        margin-left: 1.5rem;
        text-align: right;
      }
      &.fixed {
        flex-shrink: 0;
        white-space: nowrap;
      }
      &.increase {
        color: $red;
      }
      &.decrease {
        color: $blue;
      }
    }
  }
  .tab__swiper {
    min-height: calc(100% - 4rem);
  }
}
// 
.my-coupon {
  height: 100%;
  padding-top: 0.8rem;

  .tab__swiper {
    min-height: calc(100% - 4.8rem);
  }

  &__list {
    .card-item {
      margin-bottom: 1rem;
      &.disabled {
        dt {
          img {
            opacity: 0.3;
          }
        }
      }
    }
  }

  &-detail {
    margin-top: 2rem;
    &__thumbnail {
      .no-image {
        height: 25rem;
      }
      img {
        width: 100%;
      }
    }
    &__contents {
      padding: 2.6rem 1.6rem 0.5rem;
    }
    &__top {
      margin-bottom: 1.5rem;
      &-title {
        display: block;
        color: $black;
        @extend .font-size-18;
        line-height: 3rem;
        word-break: keep-all;
        margin-bottom: 1rem;
      }
      &-tag {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        margin-bottom: 2rem;
        font-size: 0;
        .tag {
          margin-right: 1rem;
          white-space: nowrap;
        }
        &-info {
          display: inline-block;
          color: $gray-6;
          @extend .font-size-14;
          line-height: 1.9rem;
          word-break: keep-all;
        }
      }
      &-date {
        color: $gray-9;
        line-height: 2.25rem;
        @extend .font-size-14;
      }
      &-point {
        margin-bottom: 1rem;
        font-size: 3.5rem;
        line-height: 3rem;
        font-weight: 700;
        line-height: 1.61;
      }
    }
    &__text {
      word-break: keep-all;
      &-sub {
        margin-bottom: 2.5rem;
        position: relative;
        padding-left: 0.9rem;
        @extend .font-size-14;
        color: $gray-9;
        line-height: 2.25rem;
        &::before {
          position: absolute;
          content: '*';
          left: 0;
          top: 0;
        }
      }
      &-cont {
        margin-bottom: 2.5rem;
        color: $gray-6;
        @extend .font-size-16;
        word-break: keep-all;
        line-height: 1.66;
      }
      &-title {
        margin-bottom: 1.2rem;
        @extend .kb-font;
        font-weight: 700;
        color: $black;
        line-height: 1;
      }
      &-list {
        li {
          position: relative;
          padding-left: 0.9rem;
          margin-bottom: 1.2rem;
          word-break: keep-all;
          &::before {
            position: absolute;
            content: '';
            left: 0;
            top: 1.1rem;
            width: 0.4rem;
            height: 0.4rem;
            border-radius: 50%;
            background-color: $gray-6;
          }
        }
      }
    }
    &__code {
      padding-top: 1rem;
      margin-bottom: 1.5rem;
      &-barcord {
        padding: 0 1.4rem;
        margin-bottom: 2.3rem;
        img {
          width: 100%;
        };
      }
    }
  }
}
.my-product {
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    dt {
      width: 8rem;
      flex-shrink: 0;
      margin-right: 1rem;
      img {
        width: 100%;
      }
    }
    dd {
      flex-grow: 1;
    }
  }
}
//  
.my-payment {
  height: 100%;
  padding-top: 0.8rem;

  .tab__swiper {
    min-height: calc(100% - 4rem);
  }

  &__title {
    margin-bottom: 1rem;
    @extend .font-size-16;
    word-break: keep-all;
  }

  &__item {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @extend .font-size-16;
    li {
      &.fixed {
        text-align: right;
        margin-left: 1.5rem;
        flex-shrink: 0;
        white-space: nowrap;
        color: $gray-9;
      }
    }
  }
  &__top {
    &-info {
      display: flex;
      margin-bottom: 1.7rem;
      @extend .font-size-14;
      line-height: 2.25rem;
    }
    &-date {
      white-space: nowrap;
      flex-shrink: 0;
    }
    &-code {
      position: relative;
      margin-left: 1rem;
      padding-left: 1.1rem;
      &::before {
        position: absolute;
        top: 0.7rem;
        left:0;
        width: 0.1rem;
        height: 1rem;
        background: rgba($white, 0.5);
        content: '';
      }
    }
    &-title {
      word-break: keep-all;
      @extend .font-size-16;
      line-height: 2.65rem;
    }
  }
  &-detail {
    padding: 2rem 1.6rem;
    padding: {
      bottom: calc(constant(safe-area-inset-bottom) + 2rem);
      bottom: calc(env(safe-area-inset-bottom) + 2rem);
    }
    dl {
      display: flex;
      justify-content: space-between;
      align-items: center;
      dt {
        white-space: nowrap;
        min-width: 6rem;
        @extend .font-size-14;
        color: $gray-6;
        line-height: 2.25rem;
      }
      dd {
        margin-left: 1.5rem;
        flex-grow: 1;
        text-align: right;
        @extend .font-size-14;
        color: $black;
        line-height: 2.65rem;
      }
    }
    &__pay {
      margin-bottom: 0.5rem;
      dl {
        dd {
          @extend .font-size-25;
          font-weight: 700;
          line-height: 1.5em;
        }
      }
    }
    &__list {
      dl {
        padding: 1.5rem 0;
        border-bottom: 1px solid $border-color-d;
        &:last-child {
          border: none;
        }
      }
    }
    &__method {
      padding: 1.5rem;
      color: $gray-6;
      @extend .font-size-14;
      line-height: 2.25rem;
      background-color: $bg-color-f;
      border-radius: 1rem;
      &-title {
        margin-bottom: 1rem;
      }
    }
    &__text {
      word-break: keep-all;
      &-sub {
        margin-top: 1rem;
        position: relative;
        padding-left: 0.9rem;
        @extend .font-size-14;
        color: $gray-9;
        line-height: 2.25rem;
        &::before {
          position: absolute;
          content: '*';
          left: 0;
          top: 0;
        }
      }
    }
    &__button {
      margin-top: 2rem;
    }
  }
}

// 
.my-program {
  &__list {
    .card-item {
      margin-bottom: 1rem;
      .tag {
        width: 4.05rem;
      }
    }
  }
}