@charset "utf-8";

.modal-popup {
  display: none;
  overflow: hidden;

  &__bg {
    position: fixed;
    top: 0;
    left: 0;
    z-index: $dialog-z-index - 1;
    width: 100%;
    height: 100%;
    background: $black;
    opacity: .75;
    display: none;
  }

  &__outer {
    position: fixed;
    top: 0;
    left: 0;
    z-index: $dialog-z-index;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__container {
    position: relative;
    width: 90%;
    background: $white;
    padding: 2rem;
    opacity: 0;
    transition: all $base-duration;

    .active & {
      opacity: 1;
    }
  }

  &__close {
    position: absolute;
    top: -0.5rem;
    right: -0.5rem;
  }

  &__header {
    position: relative;
    margin-bottom: 2.5rem;
    height: 2rem;
  }

  &__title {
    @extend .kb-font;
    @extend .font-size-16;
    font-weight: bold;
  }

  &__body {
    @extend .font-size-14;

    line-height: 1.4;
    max-height: calc(100vh - 15rem);
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
  }

  &__footer {
    text-align: center;
    margin-top: 3rem;
    // button 
    font-size: 0;

    button + button {
      margin-left: 1.2rem;
    }
  }

  /*  */
  &--alert &__container {
    padding: 2.5rem 2rem;
  }

  &--alert &__header {
    height: 2rem;
  }

  &--alert &__footer {
    margin-top: 2rem;
  }

  &--alert &__body {
    overflow-y: hidden;
  }

  &__alert-message {
    font-size: 1.6rem;
    text-align: center;
    line-height: 1.66;
    word-break: keep-all;
    color: $black;
  }
}

.toast {
  position: fixed;
  left: 0;
  bottom: 0;
  right: 0;
  text-align: right;
  z-index: $toast-z-index;
  padding: 1.4rem 1.5rem;

  &__item {
    margin-top: 1.25rem;
    padding: 1.4rem 1.5rem;
    display: none;
    background: $black;
    border-radius: 0.8rem;
    text-align: left;

    > span {
      color: $white;
      font-size: 1.6rem;
      line-height: 1.666;
    }
  }

  &.has-header {
    bottom: 6rem;
  }
}

.tooltip {
  &-add {
    display: none;
    width: 0;
    height: 0;
    overflow: hidden;
  }

  &-button {
    position: relative;
    display: inline-block;
    line-height: 1;
  }

  &-modal {
    position: absolute;
    left: 0;
    width: 100%;
    z-index: $dialog-z-index;
    padding: 1.1rem 0;

    &.active {
      opacity: 1;
    }

    &__outer {
      position: fixed;
      top: 0;
      left: 0;
      z-index: $dialog-z-index;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    &__box {
      position: relative;
      border: 1px solid $border-color-d;
      padding: 2rem 1.6rem;
      background: $white;
      max-width: 328px;
      margin: 0 auto;
    }

    &__close {
      position: absolute;
      right: 1.4rem;
      top: 1rem;
    }

    &__title {
      @extend .kb-font;
      @extend .font-size-16;
      font-weight: bold;
      margin-bottom: 1.2rem;
    }

    &__message {
      @extend .font-size-16;
      line-height: 1.65625;
      color: $gray-6;
    }
  }
}
